<template>
    <table role="table" class="p-datatable p-component p-datatable-sm">
        <thead class="p-datatable-thead" role="rowgroup">
            <tr role="row">
                <th role="cell">Rehabilitation Resource Predictor</th>
                <th role="cell">Predicted Value</th>
            </tr>
        </thead>
        <tbody class="p-datatable-tbody" role="rowgroup">
            <tr class="" tabindex="0" role="row">
                <td role="cell">Points of Physical FS Change</td>
                <td role="cell">{{getAssessmentAttribute("PredictedChange")}}</td>
            </tr>	
            <tr class="" tabindex="0" role="row">
                <td role="cell">Discharge FS Score</td>
                <td role="cell">{{primaryAssessment.StagingInfo.PredictedStage.Measure}}</td>
            </tr>	
            <tr class="" tabindex="0" role="row">
                <td role="cell">Visits per Episode</td>
                <td role="cell">{{getAssessmentAttribute("Visits")}}</td>
            </tr>	
            <tr class="" tabindex="0" role="row">
                <td role="cell">Duration in Days</td>
                <td role="cell">{{getAssessmentAttribute("Duration")}}</td>
            </tr>													
            <tr class="" tabindex="0" role="row">
                <td role="cell">Average Satisfaction Score</td>
                <td role="cell">{{getAssessmentAttribute("Satisfaction")}}%</td>
            </tr>
        </tbody>
    </table>
    
    <table role="table" class="m-t-2 p-datatable p-component p-datatable-sm">
        <thead class="p-datatable-thead" role="rowgroup">
            <tr role="row">
                <th role="cell" colspan="12">Interpretation of Predicted Value</th>
            </tr>
        </thead>
        <tbody class="p-datatable-tbody" role="rowgroup">
        <tr tabindex="0" role="row">
            <td role="cell" colspan="12">{{generateInterpretation()}}</td>
        </tr>
        </tbody>
    </table>
</template>

<script lang="ts">
import { PropType } from 'vue';
import { defineComponent } from '@vue/runtime-core';
import { FunctionalAssessment } from '../../models/foto/functionalAssessment';
import { AssessmentAttribute } from '../../models/foto/assessmentAttribute';

export default defineComponent({
    name: 'AdditionalItemTable',
    props: {
        primaryAssessment: Object as PropType<FunctionalAssessment>,
    },
    methods: {
        getAssessmentAttribute(key: string) {
            const attribute: Array<AssessmentAttribute> = this.primaryAssessment.AssessmentAttributes.filter(
                (aa: AssessmentAttribute) => aa.Attribute.toLowerCase() == key.toLowerCase(),
            );

            return attribute.length > 0 ? attribute[0].Value : '';
        },
        generateInterpretation() {
            const predictedChange: number = parseInt(this.getAssessmentAttribute('PredictedChange'));
            const higherOrLowerTxt: string = predictedChange > 0 ? 'higher' : 'lower';
            const predictedChangeObj = this.primaryAssessment.StagingInfo.PredictedStage;
            const dischargeFsScore = predictedChangeObj.Measure;
            const dischargeStage = predictedChangeObj.Stage;

            return `Given this patients risk-adjustment variables, and the actual Intake FS score, FOTO predicts this
                patient will experience at least an ${higherOrLowerTxt === 'higher' ? 'increase' : 'decrease'} in 
                function of ${predictedChange} points (to ${dischargeFsScore} or ${higherOrLowerTxt}), putting them in 
                the Stage ${dischargeStage} level or ${higherOrLowerTxt} at discharge.`;
        },
    },
});
</script>