import Patient from "@/models/foto/patient";
import PatientService from "@/services/foto/patientService";
import Client from "fhirclient/lib/Client";
import { reactive, readonly } from "vue";
import ArtifactStore from "./artifactStore";
import CodeListStore from "./codeListStore";
import EpisodeStore from "./episodeStore";
import FhirStore from "./fhirStore";

export default class ApplicationStore {
    private _context: any = reactive({
        State: {},
        Log: []
    });

    public Episode: EpisodeStore;
    public Code: CodeListStore;
    public Artifact: ArtifactStore;
    public Fhir: FhirStore;
    public Context: any = readonly(this._context);
    
    async InitAsync(fhirClient: Client){
        this.Fhir = new FhirStore();
        await this.Fhir.InitAsync(fhirClient);
        
        const outboundPatient = Patient.FromResource(this.Fhir.State.Context.Patient, '');
        const patientService = new PatientService(outboundPatient.ExternalID as string);
        
        await patientService.Create(outboundPatient);

        this.Code = new CodeListStore(outboundPatient.ExternalSiteID as string);
        this.Artifact = new ArtifactStore(outboundPatient.ExternalID as string);
        this.Episode = new EpisodeStore(outboundPatient.ExternalID as string);
        
        await this.Episode.ForPatient(outboundPatient.ExternalID as string);

        this._context.State = {
            LaunchedBy: this.Fhir.State.Context.LaunchedByResource,
            Practitioner: this.Fhir.State.Context.Practitioner,
            Patient: this.Fhir.State.Context.Patient,
            Encounter: this.Fhir.State.Context.Encounter,
            Episode: this.Fhir.State.Context.EpisodeOfCare,
            Location: this.Fhir.State.Context.Location,
            FotoPatient: outboundPatient
        };
    }

    async Refresh(fhirClient: Client){
        const store = new ApplicationStore();
        store._context.Log = this._context.Log;

        const cache_keys = await caches.keys();
        for(const key of cache_keys){
            await caches.delete(key);
        }

        await store.InitAsync(fhirClient);

        return store;
    }

    SetContextValue(key: string, value: any, serialize = true){
        this._context[key] = value;
        this.AddLog(`Set value ${key}`, value, serialize);
    }

    GetContextValue(key: string){
        return this._context.State[key];
    }

    AddLog(log: string, data: any = null, serialize = true){
        this._context.Log.push(`${new Date().toLocaleString()} - ${log}${data ? serialize ? ` - ${JSON.stringify(data)}` : ` - ${data}` : ''}`);
    }
}
