<template>
    <form autocomplete="off" @submit.prevent="submit(!v$.$invalid)" class="p-fluid">
		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<Calendar id="lastVisit" v-model="discharge.LastVisit" :showIcon="true" :class="{ 'p-invalid': submitted && v$.discharge.LastVisit.$invalid }"/>
				<label for="lastVisit">Last Visit</label>
			</span>
            <small v-if="(v$.discharge.LastVisit.$invalid && submitted) || v$.discharge.LastVisit.$pending.$response" class="p-error" >A last visit value is required.</small>
        </div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<InputNumber showButtons id="interruptionDays" v-model="discharge.InterruptionDays" suffix=" days" :min="0" :class="{ 'p-invalid': submitted && v$.discharge.InterruptionDays.$invalid }" />
				<label for="interruptionDays">Interruption</label>
			</span>
            <small v-if="(v$.discharge.InterruptionDays.$invalid && submitted) || v$.discharge.InterruptionDays.$pending.$response" class="p-error">An interruption days value is requred.</small>
        </div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<Textarea :autoResize="true" v-model="discharge.ClinicianComments" rows="5" />
				<label for="comments">Comments</label>
			</span>
        </div>

		<Divider type="dashed"></Divider>

		<div class="p-grid">
			<div class="p-col-12 p-md-8"></div>
			<div class="p-col-12 p-md-4">
				<Button type="submit" label="Submit" :loading="loading" icon="pi pi-check" class="" />
			</div>
		</div>
    </form>
</template>

<script lang="ts">
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import ApplicationStore from '../../stores/applicationStore';
import Discharge from '../../models/foto/discharge';

export default defineComponent({
    name: 'EpisodeDischargeForm',
    setup() {
        return {
            store: inject('store') as ApplicationStore,
            v$: useVuelidate(),
        };
    },
    props: {
        externalEpisodeId: String,
    },
    data: function(): any {
        return {
            discharge: new Discharge() as Discharge,
            submitted: false,
            loading: false,
        };
    },
    methods: {
        async submit() {
            this.submitted = true;

            try{
                if (!this.v$.discharge.$invalid) {
                    this.store.AddLog('Discharging episode', this.episode);
                    this.loading = true;
                    this.$Progress.start();

                    this.discharge.ExternalEpisodeId = this.externalEpisodeId;
                    await this.store.Episode.Discharge(this.discharge);
    
                    this.loading = false;
                    this.$emit('close');
                    this.$Progress.finish();
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
    },
    validations() {
        return {
            discharge: {
                InterruptionDays: {
                    required,
                },
                LastVisit: {
                    required,
                },
            },
        };
    },
});
</script>
