import Code from '@/models/code';
import Clinic from '@/models/foto/clinic';
import Clinician from '@/models/foto/clinician';
import ApiService from '@/services/api/apiService';

export default class CodeService {
    private readonly _apiService: ApiService;

    constructor(cachesKey: string){
        this._apiService = new ApiService("code-" + cachesKey);
    }

    async GetCodes(listName: string) : Promise<Array<Code>>{
        const response = await this._apiService.Fetch<Array<any>>(new Request(`/api/Foto/episode/${listName}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }));

        return response.map((code: any) => new Code(code.Key, code.Value));
    }

    async GetClinics() : Promise<Array<Clinic>>{
        return await this._apiService.Fetch<Array<any>>(new Request(`/api/Foto/clinic/json/`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }));
    }

    async GetClinicians() : Promise<Array<Clinician>>{
        return await this._apiService.Fetch<Array<any>>(new Request(`/api/Foto/clinician/json/`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }));
    }
}
