import Discharge from "@/models/foto/discharge";
import Episode from "@/models/foto/episode";
import SurveyDetails from "@/models/foto/surveyDetails";
import Visit from "@/models/foto/visit";
import EpisodeService from "@/services/foto/episodeService";
import { reactive, readonly } from "vue";

export default class EpisodeStore {
    private EpisodeState: any = reactive({
        Episodes: [] as Array<Episode>
    });
    
    private readonly _externalPatientId: string;
    private readonly _episodeService: EpisodeService;

    constructor(externalPatientId: string){
        this._externalPatientId = externalPatientId;
        this._episodeService = new EpisodeService(this._externalPatientId);
    }

    public State: any = readonly(this.EpisodeState);
    
    FilterLocal(filterFunc: Function){
        return this.EpisodeState.Episodes.filter((e: Episode) => filterFunc(e));
    }

    Get(episodeId: Number) : Episode{
        return this.EpisodeState.Episodes.filter((e: Episode) => e.EpisodeId == episodeId)[0];
    }

    async DeleteClaim(episodeId: number, externalEpisodeId: string){
        const episode = this.Get(episodeId);
        return await this._episodeService.DeleteClaim(episode, externalEpisodeId);
    }

    async Claim(episodeId: number, externalEpisodeId: string){
        const episode = this.Get(episodeId);
        const existingEpisodes = this.EpisodeState.Episodes.filter((e: Episode) => e.ExternalEpisodeId.startsWith(externalEpisodeId));

        if(existingEpisodes.length > 0)
            externalEpisodeId += `_${existingEpisodes.length}`;

        return await this._episodeService.Claim(episode, externalEpisodeId);
    }

    async ForPatient(externalPatientId: string) : Promise<Array<Episode>>{
        this.EpisodeState.Episodes = (await this._episodeService.GetAll(externalPatientId)).map((e: Episode) => Object.assign(new Episode(), e));
        return this.EpisodeState.Episodes;
    }

    GetByExternalId(externalId: string): Episode {
        return this.EpisodeState.Episodes.filter((e: Episode) => e.ExternalEpisodeId == externalId)[0];
    }

    async GetSurvey(episodeId: string | Number) : Promise<SurveyDetails>{
        return await this._episodeService.GetSurvey(episodeId.toString());
    }

    async AddVisit(visit: Visit) : Promise<any>{
        return await this._episodeService.AddVisit(visit);
    }

    async Add(episode: Episode) : Promise<any>{
        const existingEpisodes = this.EpisodeState.Episodes.filter((e: Episode) => e.ExternalEpisodeId.startsWith(episode.ExternalEpisodeId));

        if(existingEpisodes.length > 0)
            episode.ExternalEpisodeId += `_${existingEpisodes.length}`;

        const result: any = await this._episodeService.Create(episode);

        if(result.ValidEpisode && result.EpisodeCreationResponse.EpisodeId)
            this.EpisodeState.Episodes.push(episode);

        return result;
    }

    async Discharge(discharge: Discharge) : Promise<any>{
        const episode = this.GetByExternalId(discharge.ExternalEpisodeId);
        return await this._episodeService.Discharge(episode, discharge);
    }
}
