import Artifact from '@/models/foto/artifact';
import Email from '@/models/foto/email';
import ApiService from '@/services/api/apiService';
import ToastService from '../toastService';

export default class ArtifactService {
    private readonly _apiService: ApiService;

    constructor(cachesKey: string){
        this._apiService = new ApiService("artifact-" + cachesKey);
    }

    async GetReport(artifact: Artifact): Promise<Array<any>> {
        return await this._apiService.Fetch<Array<any>>(new Request(`/api/Foto/report/json/${artifact.ReportId}/${artifact.ReportType}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }));
    }

    async GetArtifacts(externalEpisodeId: string, useCache: boolean = false) : Promise<Array<Artifact>> {
        const artifacts: Array<Artifact> = await this._apiService.Fetch<Array<Artifact>>(new Request(`/api/Foto/artifact2/json/episode/${externalEpisodeId}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }), useCache);

        return artifacts
            .map((a: Artifact) => Object.assign(new Artifact(), a))
            .sort((a: Artifact, b: Artifact) => b.SurveyDateTimeCompleted.localeCompare(a.SurveyDateTimeCompleted));
    }

    async SetVisit(artifact: Artifact, visit: number): Promise<any> {
        const response = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/ArtifactVisit`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                ExternalEpisodeId: artifact.ExternalEpisodeId,
                ReportId: artifact.ReportId,
                VisitNumber: visit
            })
        }), false);

        const artifacts = await this.GetArtifacts(artifact.ExternalEpisodeId, true);
        const changed_index = artifacts.findIndex((a: Artifact) => a.ReportId === artifact.ReportId);

        if(changed_index >= 0){
            artifact.Visit = visit;
            artifacts[changed_index].Visit = visit;
            await this._apiService.PutCache(`/api/Foto/artifact2/json/episode/${artifact.ExternalEpisodeId}`, new Response(JSON.stringify(artifacts)));
        }

        ToastService.Success("Visit number updated successfully");

        return response;
    }

    async Email(email: Email): Promise<string>{
        return await this._apiService.Fetch<any>(new Request(`/api/Foto/Survey/Email`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(email)
        }), false, 'Email sent successfully');
    }
}