import ApiResponse from '../../models/api/apiResponse';
import ToastService from '../toastService';

export default class ApiService {
    private readonly _cacheKey: string;

    constructor(cacheKey: string){
        this._cacheKey = "nh-foto-sof-" + cacheKey;
    }

    async Fetch<T>(request: Request, cache: boolean = true, successMessage: string = null): Promise<T> {
        try{
            if(cache){
                const result = await this.SearchCache(request.url);
                if(result) return result;
            }

            const response = await fetch(request);

            if(response.ok){
                const clonedResp = response.clone();
                const content = await response.json() as ApiResponse;
                
                if (!content.successful) throw new Error(content.errorMessage);
                if(successMessage) ToastService.Success(successMessage);
                
                await this.PutCache(request.url, clonedResp);
                
                return content.data as T;
            }
            else throw new Error(await response.text());
        }
        catch(error: any){
            error.request = request; 
            Promise.reject(error);
        }
    }

    async PutCache(key: string | Request, response: Response){
        const cache = await caches.open(this._cacheKey);
        await cache.put(key, response);
    }

    async SearchCache(key: string | Request){
        const cache = await caches.open(this._cacheKey);
        const match = await cache.match(key);

        if(match && match.ok){
            const content = await match.json() as ApiResponse;

            if (content.successful) 
                return content.data;
        }
    }
}
