import Client from 'fhirclient/lib/Client';
import * as R4 from 'fhir/r4';

export default class FhirService {
    private FhirClient: Client;
    private _cacheKey: string;

    constructor(client: Client) {
        this.FhirClient = client;
        this._cacheKey = "nh-foto-sof-fhir-";
    }

    public async Get<Type extends R4.Resource>(reference: string): Promise<Type> {
        const cacheKey = `${this._cacheKey}-${reference}`;
        const match = await this.SearchCache(cacheKey);

        if(match)
            return match;

        const response = await this.FhirClient.request(reference);
        await this.PutCache(cacheKey, new Response(JSON.stringify(response)));

        return response;
    }

    public async ReadPatient(): Promise<R4.Patient>{
        const cacheKey = `${this._cacheKey}-patient-${this.FhirClient.patient.id}`;
        const match = await this.SearchCache(cacheKey);

        if(match)
            return match;

        const response = await this.FhirClient.patient.read();
        await this.PutCache(cacheKey, new Response(JSON.stringify(response)));

        return response;
    }

    public async ReadUser(): Promise<R4.Practitioner>{
        const cacheKey = `${this._cacheKey}-${this.FhirClient.user.resourceType.toLowerCase()}-${this.FhirClient.patient.id}`;
        const match = await this.SearchCache(cacheKey);

        if(match)
            return match;

        const response = await this.FhirClient.user.read();
        await this.PutCache(cacheKey, new Response(JSON.stringify(response)));

        return response as R4.Practitioner;
    }

    public GetState(key: string){
        return this.FhirClient.getState(`tokenResponse.${key}`);
    }

    private async PutCache(key: string | Request, response: Response){
        const cache = await caches.open(this._cacheKey);
        await cache.put(key, response);
    }

    private async SearchCache(key: string | Request){
        const cache = await caches.open(this._cacheKey);
        const match = await cache.match(key);

        if(match && match.ok){
            console.log('match', match);
            return await match.json();
        }
    }
}
