<template>
    <form autocomplete="off" @submit.prevent="submit(!v$.$invalid)" class="p-fluid p-grid">
		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown id="clinic" @change="processSelection('Clinic', 'Alias', 'SiteId', 'SiteText', false)" v-model="episode.SiteId" :options="store.Code.State.Clinic" optionLabel="Alias" optionValue="Id" :class="{ 'p-invalid': submitted && v$.episode.SiteId.$invalid }" />
				<label for="clinic">Clinic</label>
			</span>
			<small v-if="(v$.episode.SiteId.$invalid && submitted) || v$.episode.SiteId.$pending.$response" class="p-error" >A clinic is required.</small>
        </div>
		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown id="clinician" @change="processSelection('Clinician', 'Name', 'TherapistId', 'TherapistName', false)" v-model="episode.TherapistId" :options="store.Code.State.Clinician" placeholder="p-emptylabel" optionLabel="Name" optionValue="Id" :class="{ 'p-invalid': submitted && v$.episode.TherapistId.$invalid }" />
				<label for="clinician">Clinician</label>
			</span>
			<small v-if="(v$.episode.TherapistId.$invalid && submitted) || v$.episode.TherapistId.$pending.$response" class="p-error" >A therapist is required.</small>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown id="careType" @change="processSelection('CareType', 'Display', 'CareTypeId', 'CareTypeText', true)" v-model="episode.CareTypeId" :options="store.Code.State.CareType" optionLabel="Display" optionValue="Value" :class="{ 'p-invalid': submitted && v$.episode.CareTypeId.$invalid }" />
				<label for="careType">Care Type</label>
			</span>
			<small v-if="(v$.episode.CareTypeId.$invalid && submitted) || v$.episode.CareTypeId.$pending.$response" class="p-error" >A care type is required.</small>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown :loading="isLoadingOptions" id="bodypart" @change="processSelection('BodyPart', 'Display', 'BodyPartId', 'BodyPartText', true)" v-model="episode.BodyPartId" :options="store.Code.State.BodyPart" optionLabel="Display" optionValue="Value" :class="{ 'p-invalid': submitted && !episode.BodyPartId && !episode.ImpairmentId }" />
				<label for="bodypart">Body Part/Condition</label>
			</span>
			<small v-if="!episode.BodyPartId && !episode.ImpairmentId && submitted" class="p-error" >A body part or impairment is required.</small>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown :loading="isLoadingOptions" id="impairment" @change="processSelection('Impairment', 'Display', 'ImpairmentId', 'ImpairmentText', true)" v-model="episode.ImpairmentId" :options="store.Code.State.Impairment" optionLabel="Display" optionValue="Value" :class="{ 'p-invalid': submitted && !episode.BodyPartId && !episode.ImpairmentId }" />
				<label for="impairment">Impairment</label>
			</span>
			<small v-if="!episode.BodyPartId && !episode.ImpairmentId && submitted" class="p-error" >An impairment or body part is required.</small>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown :loading="isLoadingOptions" id="surgeryType" @change="processSelection('SurgeryType', 'Display', 'SurgeryTypeId', 'SurgeryTypeText', true)" v-model="episode.SurgeryTypeId" :options="store.Code.State.SurgeryType" optionLabel="Display" />
				<label for="surgeryType">Surgery Type</label>
			</span>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown id="payerSource" v-model="episode.PayerSourceId" @change="processSelection('PayerSource', 'Display', 'PayerSourceId', 'PayerSourceText', false)" :options="store.Code.State.PayerSource" optionLabel="Display" optionValue="Value" :class="{ 'p-invalid': submitted && v$.episode.PayerSourceId.$invalid }" />
				<label for="payerSource">Payer Source</label>
			</span>
			<small v-if="(v$.episode.PayerSourceId.$invalid && submitted) || v$.episode.PayerSourceId.$pending.$response" class="p-error">A payer source is required.</small>
		</div>

		<div class="p-field p-col-12 p-md-6">
			<span class="p-float-label">
				<Dropdown id="payerSource" :loading="isLoadingOptions" @change="processSelection('PrimaryMeasure', 'Display', 'PrimaryMeasureId', 'PrimaryMeasureText', true)" v-model="episode.PrimaryMeasureId" :options="store.Code.State.PrimaryMeasure" optionLabel="Display" optionValue="Value" />
				<label for="payerSource">Primary Measure</label>
			</span>
		</div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<MultiSelect :showToggleAll="false" :loading="isLoadingOptions" v-model="selectedSecondaryMeasures" :options="store.Code.State.SecondaryMeasure" optionLabel="Display" :filter="true" class="multiselect-sec-measure">
					<template #value="slotProps">
						<div class="sec-measure-item-value" v-for="option of slotProps.value" :key="option.code">
							<div>{{option.Display}}</div>
						</div>
						<template v-if="!slotProps.value || slotProps.value.length === 0">
						</template>
					</template>
					<template #option="slotProps">
						<div>
							<div>{{slotProps.option.Display}}</div>
						</div>
					</template>
				</MultiSelect>
				<label for="clinic">Secondary Measure</label>
			</span>
        </div>
		
        <div class="p-field p-col-12 p-md-12">
            <span class="p-float-label">
                <Dropdown id="externalEpisodeId" v-model="selectedExternalIdOption" :options="store.Fhir.State.Context.EpisodeOfCare" optionLabel="active">
                    <template #value="slotProps">
                        <div v-if="slotProps.value">
                            {{slotProps.value.period.start}} {{slotProps.value.period.end ? ' - ' + slotProps.value.period.end : ''}} {{slotProps.value.type[0]?.text}}
                        </div>
                        <div v-else>
                            &nbsp;
                        </div>
                    </template>
                    <template #option="slotProps">
                        <div>{{slotProps.option.period.start}} {{slotProps.option.period.end ? ' - ' + slotProps.option.period.end : ''}} {{slotProps.option.type[0]?.text}}</div>
                    </template>
                </Dropdown>
                <label for="externalEpisodeId">Epic Episode</label>
            </span>
            <small v-if="!selectedExternalIdOption?.id && submitted" class="p-error center">An epic episode is required.</small>
        </div>


        <Divider type="dashed"></Divider>

		<div class="p-col-12 p-md-8"></div>
		<div class="p-col-12 p-md-4">
			<Button type="submit" label="Submit" :loading="loading" icon="pi pi-check" class="" />
		</div>
    </form>
</template>

<script lang="ts">
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import Episode from '../../models/foto/episode';
import Code from '../../models/code';
import ApplicationStore from '../../stores/applicationStore';

export default defineComponent({
    name: 'EpisodeCreationForm',
    setup() {
        const store = inject('store') as ApplicationStore;
        const patient = store.GetContextValue('FotoPatient');

        return {
            loading: false,
            store: store,
            patient: patient,
            v$: useVuelidate(),
        };
    },
    data: function(): any {
        return {
            selectedExternalIdOption: null,
            selectedSecondaryMeasures: null,
            episode: new Episode(),
            submitted: false,
            isLoadingOptions: false,
        };
    },
    async created() {
        await this.store.Code.SetClinics();
        await this.store.Code.SetClinicians();
        await this.store.Code.SetCareTypes();
        await this.store.Code.SetPayerSource();

        const episodes = this.store.Fhir.State.Context.EpisodeOfCare;
        if (episodes?.length === 1) this.selectedExternalIdOption = episodes[0];
    },
    methods: {
        async processSelection(codeSet: string, codeTextField: string, episodeIdField: string, episodeTextField: string, updateOptions: boolean) {
            try{
                const id = this.episode[episodeIdField];
                const code = this.store.Code.State[codeSet].filter((c: any) => c?.Id == id || c?.Value == id)[0];
    
                this.episode[episodeTextField] = code[codeTextField];
    
                if (updateOptions) await this.updateOptions(codeSet);
            }
            catch(er){
                Promise.reject(er);
            }
        },
        async submit() {
            this.submitted = true;
            let externalId = this.selectedExternalIdOption?.id;

            try{
                if (!this.v$.episode.$invalid && externalId) {
                    this.loading = true;
                    this.$Progress.start();
    
                    const existingEp = this.store.Episode.FilterLocal((e: Episode) => { e.ExternalEpisodeId === externalId });
    
                    if(existingEp && existingEp.length > 0) externalId += `_${existingEp.length}`;
    
                    this.episode.SecondaryBallotText = this.episode.SecondaryMeasureIds.map((sp: Code) => sp.Display).join(',');
                    this.episode.SecondaryMeasureIds = this.selectedSecondaryMeasures.map((sp: Code) => sp.Value);
                    this.episode.CreateDate = new Date();
                    this.episode.ExternalPatientId = this.patient.ExternalID;
                    this.episode.ExternalEpisodeId = externalId;
                    this.store.AddLog('Creating episode', this.episode);
                    await this.store.Episode.Add(this.episode);
    
                    this.$emit('close');
                    this.$Progress.finish();
                    this.loading = false;
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
        updateOptions: async function(updatedField: string): Promise<void> {
            this.isLoadingOptions = true;

            try{
                this.store.AddLog('Updating option values for episode creation, with the current values set as', this.episode);

                if (updatedField.toLocaleLowerCase() === 'caretype') {
                    this.episode.BodyPartId = null;
                    this.episode.ImpairmentId = null;
                    this.episode.PrimaryMeasureId = null;
                    this.episode.SecondaryMeasureIds = [];
                } else if (updatedField.toLocaleLowerCase() === 'bodypart') {
                    this.episode.ImpairmentId = null;
                    this.episode.PrimaryMeasureId = null;
                    this.episode.SecondaryMeasureIds = [];
                } else if (updatedField.toLocaleLowerCase() === 'impairment') {
                    this.episode.PrimaryMeasureId = null;
                    this.episode.SecondaryMeasureIds = [];
                }
    
                let impairments: Array<Code> = [];
    
                if (this.episode.CareTypeId !== null) {
                    await this.store.Code.SetBodyParts(this.episode.CareTypeId);
    
                    if (this.episode.BodyPartId !== null) {
                        impairments = await this.store.Code.SetImpairments(this.episode.CareTypeId, this.episode.BodyPartId);
    
                        if (this.episode.ImpairmentId !== null) 
                            await this.store.Code.SetSurgeryTypes(this.episode.BodyPartId, this.episode.ImpairmentId);
                    } 
                    else 
                        impairments = await this.store.Code.SetImpairments(this.episode.CareTypeId);
                }
    
                if (impairments?.length === 1) {
                    this.episode.ImpairmentId = impairments[0].Value;
                    this.processSelection('Impairment', 'Display', 'ImpairmentId', 'ImpairmentText', false);
                }
    
                if (this.episode.SiteId !== null && ((this.episode.BodyPartId !== null && this.episode.BodyPartId !== -1) || (this.episode.ImpairmentId !== null && this.episode.ImpairmentId !== -1))) {
                    const primaryMeasures = await this.store.Code.SetPrimaryMeasure(
                        this.episode.SiteId
                        , this.episode.CareTypeId
                        , this.episode.BodyPartId !== null ? this.episode.BodyPartId : -1
                        , this.episode.ImpairmentId !== null ? this.episode.ImpairmentId : -1,
                    );
    
                    if (primaryMeasures?.length > 0) {
                        this.episode.PrimaryMeasureId = primaryMeasures[0].Value;
                        this.processSelection('PrimaryMeasure', 'Display', 'PrimaryMeasureId', 'PrimaryMeasureText', false);
                    }
    
                    if (this.episode.PrimaryMeasureId !== null) {
                        await this.store.Code.SetSecondaryMeasure(
                            this.episode.SiteId
                            , this.episode.CareTypeId
                            , this.episode.BodyPartId !== null ? this.episode.BodyPartId : -1
                            , this.episode.ImpairmentId !== null ? this.episode.ImpairmentId : -1
                            , this.episode.PrimaryMeasureId,
                        );
                    }
                }
            }
            catch(er){
                Promise.reject(er);
            }

            this.isLoadingOptions = false;
        },
    },
    validations() {
        return {
            episode: {
                SiteId: {
                    required,
                },
                TherapistId: {
                    required,
                },
                CareTypeId: {
                    required,
                },
                PayerSourceId: {
                    required,
                },
            },
        };
    },
});
</script>