const HL7 = require('hl7-standard');

export default class Hl7Service {
    static Mdm: any = {
        Build: (patientId: string, pFName: string, pLName: string, encounterId: string, externalId: string, base64: string) => {
            const message = new HL7();
            const dateTime = new Date().toISOString().split('.')[0].replace(/[^\d]/gi, '');
            
            message.createSegment('MSH');
            message.set('MSH.2', '^~\\&'); //Encoding
            message.set('MSH.3', 'FOTO'); //Sending Application
            message.set('MSH.5', 'FOTO'); //Sending Application
            message.set('MSH.6', 'EPIC'); //Receiving Application
            message.set('MSH.7', dateTime); //Date/Time of Message
            message.set('MSH.9.1', 'MDM'); //Message Type
            message.set('MSH.9.2', 'T02'); //Message Type
            message.set('MSH.10', dateTime); //Message Control ID
            message.set('MSH.11', 'D'); //Processing ID
            message.set('MSH.12', '2.3'); //Version ID
    
            message.createSegment('PID');
            message.set('PID.3.1', patientId); //Patient Identifier
            message.set('PID.3.5', 'EPI'); //Patient Identifier
            message.set('PID.5.1', pLName); //Patient Name - Last
            message.set('PID.5.2', pFName); //Patient Name - First
    
            message.createSegment('PV1');
            message.set('PV1.1', '1');
            message.set('PV1.19', encounterId); //Visit Number
    
            message.createSegment('TXA');
            message.set('TXA.2', 'Questionnaire'); //Report Type
            message.set('TXA.4', dateTime); //Activity Date/Time
            message.set('TXA.12', externalId); //External Unique Document Number
            message.set('TXA.17', 'AU'); //Document Completion Status
            message.set('TXA.19', 'AV'); // Document Availability Status
    
            message.createSegment('OBX');
            message.set('OBX.1', '1'); // Set ID
            message.set('OBX.2', 'ED'); //Value Type
            message.set('OBX.3', 'PDF_FILE');
            message.set('OBX.4', '1');
            message.set('OBX.5.3', 'PDF'); //Observation Value
            message.set('OBX.5.4', 'Base64'); //Observation Value
            message.set('OBX.5.5', base64); //Observation Value
    
            return message.build();
        },
        Send: async(message: any) => {
            console.log(message);
        }
    }
}
