import Patient from '@/models/foto/patient';
import ApiResponse from '@/models/api/apiResponse';
import ApiService from '@/services/api/apiService';

export default class PatientService {
    private readonly _apiService: ApiService;

    constructor(cachesKey: string){
        this._apiService = new ApiService("patient-" + cachesKey);
    }

    async Create(patient: Patient): Promise<ApiResponse> {
        return await this._apiService.Fetch<any>(new Request(`/api/Foto/patient2/json`,
        {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: patient.ToJSON()
        }), false);
    }
}
