
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import ApplicationStore from '../../stores/applicationStore';
import Discharge from '../../models/foto/discharge';

export default defineComponent({
    name: 'EpisodeDischargeForm',
    setup() {
        return {
            store: inject('store') as ApplicationStore,
            v$: useVuelidate(),
        };
    },
    props: {
        externalEpisodeId: String,
    },
    data: function(): any {
        return {
            discharge: new Discharge() as Discharge,
            submitted: false,
            loading: false,
        };
    },
    methods: {
        async submit() {
            this.submitted = true;

            try{
                if (!this.v$.discharge.$invalid) {
                    this.store.AddLog('Discharging episode', this.episode);
                    this.loading = true;
                    this.$Progress.start();

                    this.discharge.ExternalEpisodeId = this.externalEpisodeId;
                    await this.store.Episode.Discharge(this.discharge);
    
                    this.loading = false;
                    this.$emit('close');
                    this.$Progress.finish();
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
    },
    validations() {
        return {
            discharge: {
                InterruptionDays: {
                    required,
                },
                LastVisit: {
                    required,
                },
            },
        };
    },
});
