<template>
    <Card class="layout-sidebar">
        <template #content>
            <div class="layout-profile">
                <div class="p-col-12">
                    <Card class="bs-none">
                        <template class="gender-avatar-content" #content>
                            <Avatar v-if="patient?.gender?.toLowerCase() === 'female'" class="text-pink-400 bg-pink-100 h-8-r w-8-r fs-5-r bg-s-b female-avatar" size="xlarge" shape="circle">
                                <font-awesome-icon icon="venus" />
                            </Avatar>
                            <Avatar v-else-if="patient?.gender?.toLowerCase() === 'male'" class="text-blue-400 bg-blue-100 h-8-r w-8-r fs-5-r bg-s-b male-avatar" size="xlarge" shape="circle">
                                <font-awesome-icon icon="mars" />
                            </Avatar>
                            <Avatar v-else class="h-8-r w-8-r fs-5-r bg-s-b text-purple-400 bg-purple-100" size="xlarge" shape="circle">
                                <font-awesome-icon icon="genderless" />
                            </Avatar>
                        </template>
                        <template #footer>
                            <h2 class="ta-c m-b-_5 m-t-1">{{patient?.name[0]?.text}}</h2>
                            <p class="ta-c fw-500 text-500">{{age}} year old {{patient?.gender?.toLowerCase()}}</p>
                        </template>
                    </Card>
                </div>
            </div>
            <div class="layout-menu-container">
                <div class="p-col-12">
                    <Accordion :multiple="false">
                        <AccordionTab>
                            <template #header>
                                <font-awesome-icon class="m-r-5 fs-1_2 min-w-1" icon="user" />
                                <span>Demographics</span>
                            </template>
                            <DataTable :autoLayout='true' :value="demographics" class="p-datatable-sm app-menu-demographic-table hide-header br-tl-5 br-tr-5" responsiveLayout="scroll">
                                <template #header>
                                    <h5 class="p-m-0 ta-c">General</h5>
                                </template>
                                <Column field="key" />
                                <Column class="ta-r" field="value" />
                            </DataTable>

                            <DataTable :autoLayout='true' v-if="patient?.address.length > 0" :value="patient.address" class="p-datatable-sm hide-header br-bl-5 br-br-5" responsiveLayout="scroll">
                                <template #header>
                                    <h5 class="p-m-0 ta-c">Address</h5>
                                </template>
                                <Column field="use">
                                    <template #body="slotProps">{{capitalizeFirstLetter(slotProps.data.use)}}</template>
                                </Column>
                                <Column class="ta-r" field="line[0]">
                                    <template #body="slotProps">{{slotProps.data.line[0]}}, {{slotProps.data.city}}, {{slotProps.data.state}}, {{slotProps.data.postalCode}}</template>
                                </Column>
                            </DataTable>

                            <DataTable :autoLayout='true' v-if="patient?.telecom.length > 0" :value="patient.telecom" class="p-datatable-sm hide-header br-bl-5 br-br-5" responsiveLayout="scroll">
                                <template #header>
                                    <h5 class="p-m-0 ta-c">Communication</h5>
                                </template>
                                <Column field="system">
                                    <template #body="slotProps">{{capitalizeFirstLetter(slotProps.data.system)}}</template>
                                </Column>
                                <Column class="ta-r" field="value" />
                            </DataTable>
                        </AccordionTab>
                        <AccordionTab>
                            <template #header>
                                <font-awesome-icon class="m-r-5 fs-1_2 min-w-1" icon="book-medical" />
                                <span>Episode</span>
                            </template>
                            <div v-if="d_store.Fhir.State.Context.EpisodeOfCare?.length > 0">
                                <DataTable :autoLayout='true' v-for="ep in d_store.Fhir.State.Context.EpisodeOfCare" :key="ep.id" :value="populateEpisodeDetails(ep)" class="p-datatable-sm hide-header br-bl-5 br-br-5" responsiveLayout="scroll">
                                    <template #header>
                                        <h5 class="p-m-0 ta-c">{{`${ep.period.start}${ep.period.end ? ' - ' + ep.period.end : ''}`}}</h5>
                                    </template>
                                    <Column field="key" />
                                    <Column class="ta-r" field="value" />
                                </DataTable>
                            </div>
                            <div class="p-datatable p-component p-datatable-gridlines p-datatable-sm" v-else>
                                <div class="p-datatable-header">
                                    <h5 class="p-m-0 ta-c">Associated Episodes</h5>
                                </div>

                                <div class="p-datatable-wrapper">
                                    <table role="table" class="p-datatable-table">
                                        <tbody role="rowgroup" class="p-datatable-tbody">
                                            <tr role="row" colspan="12">
                                                <td role="cell" class="ta-c" colspan="12">No active episodes found</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </AccordionTab>
                        <AccordionTab>
                            <template #header>
                                <font-awesome-icon class="m-r-5 fs-1_2 min-w-1" icon="calendar-day" />
                                <span>Encounter</span>
                            </template>
                            <DataTable :autoLayout='true' :value="encounter" class="p-datatable-sm hide-header br-bl-5 br-br-5" responsiveLayout="scroll">
                                <template #header>
                                    <h5 class="p-m-0 ta-c">Details</h5>
                                </template>
                                <Column field="key"/>
                                <Column class="ta-r" field="value" />
                            </DataTable>
                        </AccordionTab>
                        <AccordionTab v-for="list in lists" :key="list.name">
                            <template #header>
                                <font-awesome-icon class="m-r-5 fs-1_2 min-w-1" :icon="
                                    list.name === 'Allergies' ? 'allergies' 
                                    : list.name === 'Immunizations' ? 'syringe' 
                                    : list.name === 'Medications' ? 'prescription-bottle-alt' 
                                    : 'exclamation-triangle'" />
                                <span>{{list.name}}</span>
                            </template>
                            <div v-if="list.data && ((list.data.length === 1 && list.data[0].emptyReason) || list.data.length === 0)" class="p-datatable p-component p-datatable-gridlines p-datatable-sm">
                                <div class="p-datatable-header">
                                    <h5 class="p-m-0 ta-c">Active {{list.name}}</h5>
                                </div>

                                <div class="p-datatable-wrapper">
                                    <table role="table" class="p-datatable-table">
                                        <tbody role="rowgroup" class="p-datatable-tbody" :id="'list-table-body-' + list.name">
                                            <tr role="row" colspan="12">
                                                <td role="cell" class="ta-c" colspan="12">No active {{list.name}} found</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>

                            <div v-else class="p-datatable p-component p-datatable-sm">
                                <div class="p-datatable-header">
                                    <h5 class="p-m-0 ta-c">Active {{list.name}}</h5>
                                </div>

                                <div class="p-datatable-wrapper">
                                    <table role="table" class="p-datatable-table">
                                        <tbody role="rowgroup" class="p-datatable-tbody" :id="'list-table-body-' + list.name" />
                                    </table>
                                </div>
                            </div>
                        </AccordionTab>
                    </Accordion>
                </div>
            </div>
        </template>
    </Card>
</template>

<script lang="ts">
import { defineComponent } from '@vue/runtime-core';
import { EpisodeOfCare, Extension, Identifier, List } from 'fhir/r4';
import { inject } from 'vue';

export default defineComponent({
    name: 'AppSidebar',
    setup: function(): any {
        return {
            d_store: inject('store'),
        };
    },
    data() {
        return {
            lists: [
                {
                    name: 'Allergies',
                    data: (this.d_store as any).Fhir.State.Context.Allergies?.filter((a: List) => a.status === 'current'),
                    useFullRow: false,
                },
                {
                    name: 'Immunizations',
                    data: (this.d_store as any).Fhir.State.Context.Immunizations?.filter((a: List) => a.status === 'current'),
                    useFullRow: false,
                },
                {
                    name: 'Medications',
                    data: (this.d_store as any).Fhir.State.Context.Medications?.filter((a: List) => a.status === 'current'),
                    useFullRow: false,
                },
                {
                    name: 'Problems',
                    data: (this.d_store as any).Fhir.State.Context.Problems?.filter((a: List) => a.status === 'current'),
                    useFullRow: true,
                },
            ],
            patient: (this.d_store as any).Fhir.State.Context.Patient,
        };
    },
    mounted() {
        this.lists.forEach((list: any) => {
            if (list.data && list.data[0]?.entry?.length > 1) this.generateListTableBody(list.name, list.data[0].entry, list.useFullRow);
        });
    },
    computed: {
        age(){
            const today = new Date();
            const birthDate = new Date(this.patient?.birthDate);
            const age = today.getFullYear() - birthDate.getFullYear();
            const m = today.getMonth() - birthDate.getMonth();

            return m < 0 || (m === 0 && today.getDate() < birthDate.getDate()) ? age - 1 : age;
        },
        demographics(){
            return [
                {
                    key: 'Birth Day',
                    value: this.patient?.birthDate
                },
                {
                    key: 'Alias',
                    value: this.getIdentifierValue('mychartlogin')
                },
                {
                    key: 'Birth Sex',
                    value: this.getExtensionValue('us-core-birthsex')
                },
                {
                    key: 'Race',
                    value: this.getExtensionValue('us-core-race')
                },
                {
                    key: 'Ethnicity',
                    value: this.getExtensionValue('us-core-ethnicity')
                }
            ];
        },
        encounter(){
            const encounter = this.d_store.Fhir.State.Context.Encounter;
            const encounterDetails = [];
            let period = encounter?.period.start;

            if (encounter?.period.end) period += ` - ${encounter.period.end}`;

            if (period) {
                encounterDetails.push({
                    key: 'Period',
                    value: period,
                });
            }

            if (encounter?.status) {
                encounterDetails.push({
                    key: 'Status',
                    value: this.capitalizeFirstLetter(encounter.status),
                });
            }

            if (encounter?.type.length > 0) {
                encounterDetails.push({
                    key: 'Type',
                    value: encounter.type[0]?.text,
                });
            }

            if (encounter?.location.length > 0) {
                encounterDetails.push({
                    key: 'Location',
                    value: encounter.location[0].location.display,
                });
            }

            return encounterDetails;
        }
    },
    methods: {
        generateListTableBody(listName: string, data: any, useFullRow: boolean = false) {
            let value = '<tr role="row" colspan="12">';
            const items = [...new Set(data.slice().sort((a: any, b: any) => a.item.display.localeCompare(b.item.display)).map((a: any) => a.item.display))];

            if (!useFullRow) {
                let count = 0;

                items.forEach((item: any) => {
                    if (count % 2 === 0) value += `<td role="cell" colspan="6">${item}</td>`;
                    else value += `<td role="cell" colspan="6">${item}</td></tr>`;

                    count++;
                });

                if (items && items.length !== 0 && items.length % 2 !== 0) value += "<td role='cell' colspan='6'></td></tr>";
                else value += '</tr>';
            } 
            else items.forEach((item: any) => { value += `<tr><td role="cell">${item}</td></tr>`; });

            const tBody: HTMLElement = document.querySelector(`#list-table-body-${listName}`);
            if (tBody) tBody.innerHTML = value;
        },
        populateEpisodeDetails(episode: EpisodeOfCare) {
            let episodeDetails = [];
            const type = episode?.type.map((t: any) => `${t.text}`).join(', ');
            const condition = episode?.diagnosis.map((t: any) => `${t.condition.display}`).join(', ');

            if (episode?.status) {
                episodeDetails.push({
                    key: 'Status',
                    value: this.capitalizeFirstLetter(episode?.status),
                });
            }

            if (condition) {
                episodeDetails.push({
                    key: 'Condition',
                    value: condition,
                });
            }

            if (type) {
                episodeDetails.push({
                    key: 'Type',
                    value: type,
                });
            }

            return episodeDetails;
        },
        getIdentifierValue(key: string) {
            const id = this.patient?.identifier.filter((i: Identifier) => i.type?.text?.toLowerCase() === key);
            return id && id.length > 0 ? id[0].value : '--';
        },
        getExtensionValue(key: string) {
            const ext = this.patient?.extension.filter((e: Extension) => e.url.toLowerCase().includes(key))[0];
            let valueCc = null;

            if (ext) {
                if (ext.extension && ext.extension.length > 0) {
                    const ombCat = ext.extension.filter((e: Extension) => e.url.toLowerCase() === 'ombCategory')[0];

                    if (ombCat) valueCc = ombCat;
                    else return ext.extension.filter((e: Extension) => e.url.toLowerCase() === 'text')[0]?.valueString;
                } 
                else if (ext.valueString) return ext.valueString;
                else if (ext.valueCode) return ext.valueCode;
                else if (ext.valueCodeableConcept) valueCc = ext.valueCodeableConcept.coding[0];
            }

            if (valueCc) {
                if (valueCc.valueCoding.code && valueCc.valueCoding.dispaly) return `(${valueCc.valueCoding.code}) ${valueCc.valueCoding.display}`;
                else if (valueCc.valueCoding.code) return `${valueCc.valueCoding.code}`;
                else if (valueCc.valueCoding.display) return `${valueCc.valueCoding.display}`;
            }

            return '--';
        },
        capitalizeFirstLetter(string: string) {
            return string.charAt(0).toUpperCase() + string.slice(1);
        },
    },
});
</script>