import Episode from '@/models/foto/episode';
import ApiService from '@/services/api/apiService';
import Visit from '@/models/foto/visit';
import Discharge from '@/models/foto/discharge';
import SurveyDetails from '@/models/foto/surveyDetails';
import ToastService from '../toastService';

export default class EpisodeService {
    private readonly _apiService: ApiService;

    constructor(cachesKey: string){
        this._apiService = new ApiService("episode-" + cachesKey);
    }

    async Claim(episode: Episode, externalEpisodeId: string) : Promise<any>{
        const result = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/json/claim2`,
        {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                episodeId: episode.EpisodeId,
                externalEpisodeId: externalEpisodeId  
            })
        }), false);

        if(result.Success){
            const episodes = await this.GetAll(episode.ExternalPatientId);
            const episode_index = episodes.findIndex((e: Episode) => e.EpisodeId === episode.EpisodeId);
            episodes[episode_index].ExternalEpisodeId = externalEpisodeId;
            episode.ExternalEpisodeId = externalEpisodeId;

            await this._apiService.PutCache(`/api/Foto/episode/json/GetEpisodeStatus/${episode.ExternalPatientId}/Both`, new Response(JSON.stringify(episodes)));
            ToastService.Success("Episode claimed successfully.");
        }
        else
            ToastService.Error(`Unable to claim episode: ${result.Text}`);

        return result;
    }

    async DeleteClaim(episode: Episode, externalEpisodeId: string) : Promise<any>{
        const result = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/${externalEpisodeId}/${episode.EpisodeId}`,
        {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' },
        }), false);

        if(result){
            const episodes = await this.GetAll(episode.ExternalPatientId);
            const episode_index = episodes.findIndex((e: Episode) => e.EpisodeId === episode.EpisodeId);
            episodes[episode_index].ExternalEpisodeId = null;
            episode.ExternalEpisodeId = null;

            await this._apiService.PutCache(`/api/Foto/episode/json/GetEpisodeStatus/${episode.ExternalPatientId}/Both`, new Response(JSON.stringify(episodes)));
        }

        return result;
    }

    async GetAll(patientExternalId: string): Promise<Array<Episode>> {
        const response = (await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/json/GetEpisodeStatus/${patientExternalId}/Both`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        })));

        response.forEach((e: any) => { e.ExternalPatientId = patientExternalId; });

        return response.map((e: any) => Object.assign(new Episode(), e));
    }

    async GetSurvey(episodeId: string): Promise<SurveyDetails>{
        const details = new SurveyDetails();

        details.Url = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/SurveyURL/${episodeId}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }), false);

        details.LobbyCode = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/SurveyLobbyCode/${episodeId}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }), false);

        details.QrCode = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/SurveyQRcode/${episodeId}`,
        {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
        }), false);

        return details.Url === '' || details.QrCode === '' ? null : details;
    }

    async AddVisit(visit: Visit): Promise<any>{
        const result = await this._apiService.Fetch<any>(new Request(`/api/Foto/visit2/json`,
        {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(visit)
        }), false);

        if(result[0].Success)
            ToastService.Success("Visit added successfully.");
        else
            ToastService.Error(result.Text);

        return result;
    }

    async Discharge(episode: Episode, discharge: Discharge): Promise<any>{
        const response = await this._apiService.Fetch<any>(new Request(`/api/Foto/discharge/json`,
        {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(discharge)
        }), false);

        if(response.ResultCode === 0)
            ToastService.Error(response.Text);
        else if(response.ResultCode === 1)
            ToastService.Warning(response.Text);
        else{
            const episodes = await this.GetAll(episode.ExternalPatientId);
            const epiIndex = episodes.findIndex((e: Episode) => e.EpisodeId === episode.EpisodeId);
            episodes[epiIndex].DischargeTime = new Date();
            episode.DischargeTime = new Date();

            await this._apiService.PutCache(`/api/Foto/episode/json/GetEpisodeStatus/${episode.ExternalPatientId}/Both`, new Response(JSON.stringify(episodes)));            
            ToastService.Success("Episode discharged successfully.");
        }

        return response;
    }

    async Create(episode: Episode): Promise<any> {
        const result = await this._apiService.Fetch<any>(new Request(`/api/Foto/episode/json/Creation`,
        {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(episode.Json)
        }), false, 'Episode created successfully!');

        if(result.ValidEpisode && result.EpisodeCreationResponse.EpisodeId){
            episode.EpisodeId = result.EpisodeCreationResponse.EpisodeId;
            const episodes = await this.GetAll(episode.ExternalPatientId);
            episodes.push(episode);
            await this._apiService.PutCache(`/api/Foto/episode/json/GetEpisodeStatus/${episode.ExternalPatientId}/Both`, new Response(JSON.stringify(episodes)));

            ToastService.Success("Episode created successfully.");
        }
        else
            ToastService.Error(`Error creating episode: ${result.InvalidEpisodeReasons.join(', ')}`);

        return result;
    }
}