<template>
	<div class="layout-logo">
		<img alt="Logo" src="../assets/logo_anhc-foto-white.svg" />
	</div>
	<div class="layout-topbar">
		<div class="layout-topbar-icons">
			<button class="p-link">
				<Avatar v-tooltip.left="`Welcome${firstName ? ', ' + firstName : ''}`" icon="pi pi-user" size="large" :label="initials" />
			</button>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';

export default defineComponent({
	name: 'AppTopBar',
	setup() {
		const store = inject('store');
		const practitioner = store.Fhir.State.Context.Practitioner;
		let firstName = null;
		let initials = null;

		if (practitioner?.name.length > 0) {
			const lastName = practitioner?.name[0].family;

			if (practitioner?.name[0].given.length > 0) firstName = practitioner.name[0].given[0];

			if (firstName) initials = firstName[0];
			if (lastName) initials += lastName[0];
		}

		return {
			firstName,
			initials
		};
	},
});
</script>