import LaunchContext from "@/models/fhir/launchContext";
import FhirService from "@/services/fhir/fhirService";
import * as R4 from 'fhir/r4';
import { reactive, readonly } from "vue";
import Client from "fhirclient/lib/Client";

export default class FhirStore {
    private FhirState: any = reactive({
        Context: new LaunchContext(),
        ValueSet: {
            EpisodeOfCareType: []
        }
    });
    public FhirService: FhirService;
    public State: any = readonly(this.FhirState);

    async InitAsync(client: Client){
        this.FhirService = new FhirService(client);
        this.FhirState.Context.LaunchedByResource = client.user.resourceType;

        await this.UpdateState();
    }

    async UpdateState(){
        this.FhirState.Context.Practitioner = await this.SetPractitioner(true);
        this.FhirState.Context.Patient = await this.SetPatient(true);
        this.FhirState.Context.Encounter = await this.SetEncounter(this.FhirService.GetState('encounter'), true);
        this.FhirState.Context.EpisodeOfCare = await this.SetEpisodeOfCare(this.State.Context.Encounter, false);
        this.FhirState.Context.Location = await this.SetLocation(true);
        this.FhirState.Context.Allergies = await this.SetList('allergies');
        this.FhirState.Context.Problems = await this.SetList('problems');
        this.FhirState.Context.Medications = await this.SetList('medications');
        this.FhirState.Context.Immunizations = await this.SetList('immunizations');
        this.FhirState.ValueSet.EpisodeOfCareType = await this.GetValueSet('EpisodeOfCare.type');
    }

    public async EpisodeSearch(type: string){
        return await this.FhirService.Get(`EpisodeOfCare?patient=${this.FhirState.Context.Patient.id}&type=${type}`);
    }

    private async GetValueSet(name: string){
        return await this.FhirService.Get(`ValueSet/$expand?context=${name}&contextDirection=outgoing`)
    }

    private async SetList(code: string){
        const list = await this.FhirService.Get(`List?patient=${this.FhirState.Context.Patient.id}&code=${code}`);
        return (list as R4.Bundle).entry.map(e => e.resource);
    }

    private async SetPractitioner(errorIfNotFound = false) {
        const practitioner = (await this.FhirService.ReadUser()) as R4.Practitioner;

        if (!practitioner && errorIfNotFound) throw new Error('Practitioner not found in EMR');

        return practitioner;
    }

    private async SetPatient(errorIfNotFound: boolean = false) {
        const pat = await this.FhirService.ReadPatient();

        if (!pat && errorIfNotFound) throw new Error('Patient not found in EMR');

        return pat;
    }

    private async SetLocation(errorIfNotFound: boolean = false) {
        let location = null;
        const locationId = this.FhirService.GetState('loginDepartment');

        if(locationId)
            location = await this.FhirService.Get<R4.Location>(`Location/${locationId}`);

        if (!location && errorIfNotFound) throw new Error('Location not found in EMR');

        return location;
    }

    private async SetEncounter(encounterId: string, errorIfNotFound: boolean = false) {
        const enc = await this.FhirService.Get<R4.Encounter>(`Encounter/${encounterId}`);

        if (!enc && errorIfNotFound) throw new Error('Encounter not found in EMR');

        return enc;
    }

    private async SetEpisodeOfCare(encounter: R4.Encounter, errorIfNotFound: boolean = false) {
        const episodeOfCare = [];

        if (encounter?.episodeOfCare && encounter.episodeOfCare?.length > 0) {
            for(let count = 0; count < encounter.episodeOfCare.length; count++){
                const eocRef = encounter.episodeOfCare[count]?.reference;
                if (eocRef && eocRef !== '') episodeOfCare.push(await this.FhirService.Get<R4.EpisodeOfCare>(eocRef));
            }
        }

        if (episodeOfCare.length === 0 && errorIfNotFound) throw new Error('Episode of care not found in EMR');

        return episodeOfCare;
    }
}
