<template>
    <form autocomplete="off" @submit.prevent="submit" class="p-fluid">
        <div class="p-col-12 p-lg-12">
            <Fieldset legend="External ID">
                <div class="p-field p-col-12 p-md-12">
                    <DataTable :autoLayout='true' :value="episodeOptions" v-model:selection="selectedEpisode" dataKey="id" responsiveLayout="scroll" >
                        <template #header>
                            <div class="table-header p-d-flex p-flex-column p-flex-md-row p-jc-md-between">
                                <h3 class="p-mb-2 p-m-md-0 p-as-md-center">Select an Episode</h3>
                                <span class="p-float-label">
                                    <Dropdown @change="loadEpisodes" :filter="true" id="type" v-model="selectedType" :options="types" optionLabel="Display" optionValue="Value" />
                                    <label for="type">Episode Type</label>
                                </span>
                            </div>
                        </template>
                        <template #empty v-if="!selectedType">Please select an episode type from the dropdown above</template>
                        <template #empty v-else-if="selectedType && episodeOptions.length === 0">No episodes found</template>
                        <Column selectionMode="single" headerStyle="width: 3em"></Column>
                        <Column header="Diagnosis">
                            <template #body="slotProps">
                                <span>{{slotProps.data.diagnosis.map((d) => d.condition.display).join(', ')}}</span>
                            </template>                
                        </Column>
                        <Column header="Type">
                            <template #body="slotProps">
                                <span>{{slotProps.data.type.map((t) => t.text).join(', ')}}</span>
                            </template>
                        </Column>
                        <Column field="status" header="Status"></Column>
                        <Column field="period.start" header="Start"></Column>
                        <Column field="period.end" header="End"></Column>
                    </DataTable>
                </div>

                <p v-if="(!selectedEpisode && submitted)" class="p-error ta-c" style="margin-bottom:0; text-align:center;">An episode must be selected</p>
            </FieldSet>
        </div>
        
        <Divider type="dashed"></Divider>

        <div class="p-grid">
            <div class="p-col-12 p-md-10"></div>
            <div class="p-col-12 p-md-2">
                <Button type="submit" label="Submit" :loading="loading" icon="pi pi-check" class="" />
            </div>
        </div>
    </form>
</template>

<script lang="ts">
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import ApplicationStore from '../../stores/applicationStore';
import Code from '../../models/code';
import { BundleEntry, Coding, Resource } from 'fhir/r4';

export default defineComponent({
    name: 'ClaimEpisodeForm',
    setup() {
        const store = inject('store') as ApplicationStore
        const codes = store.Fhir.State.ValueSet.EpisodeOfCareType?.expansion?.contains;
        let types = [];

        if (codes && codes.length > 0)
            types = [...codes].sort((a: any, b: any) => a.display.localeCompare(b.display)).map((c: Coding) => new Code(`${c.system}|${c.code}`, `${c.display} (${c.code})`));

        return {
            store,
            types
        };
    },
    props: {
        episodeId: Number,
    },
    data: function(): any {
        return {
            loading: false,
            submitted: false,
            selectedType: null,
            selectedEpisode: null,
            episodeOptions: [],
        };
    },
    methods: {
        async loadEpisodes() {
            try{
                this.submitted = false;
                this.selectedEpisode = null;
                const options = await this.store.Fhir.EpisodeSearch(this.selectedType);
                this.episodeOptions = options.entry.map((e: BundleEntry) => e.resource).filter((e: Resource) => e.resourceType.toLowerCase() !== 'operationoutcome');
            }
            catch(er){
                Promise.reject(er);
            }
        },
        async submit() {
            this.submitted = true;

            try{
                if (this.selectedEpisode) {
                    this.store.AddLog(`Claiming episode id of ${this.episodeId} with the value of ${this.selectedEpisode.id}`);
                    this.loading = true;
                    this.$Progress.start();
    
                    this.store.Episode.Claim(this.episodeId, this.selectedEpisode.id);
    
                    this.loading = false;
                    this.$emit('close');
                    this.$Progress.finish();
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
    },
});
</script>

<style lang="scss">
.p-datatable-emptymessage > td {
	text-align: center !important;
}
</style>