
import { ChartType } from 'chart.js';
import { defineComponent, ref, inject } from '@vue/runtime-core';
import { PropType } from 'vue';
import { FilterMatchMode } from 'primevue/api';
import Artifact from '../../models/foto/artifact';
import { FunctionalAssessment } from '../../models/foto/functionalAssessment';
import { PatientReported } from '../../models/foto/patientReported';
import { StagingFunctionalityPrediction } from '../../models/foto/stagingFunctionalityPrediction';

export default defineComponent({
    name: 'RiskCriteria',
    props: {
        artifact: Object as PropType<Artifact>,
    },
    setup() {
        const basicOptions = ref({
            layout: {
                padding: 5,
            },
            elements: {
                point: {
                    radius: 1,
                },
            },
            plugins: {
                legend: {
                    display: false,
                },
                tooltip: {
                    enabled: false,
                    padding: 0,
                },
            },
            scales: {
                x: {
                    ticks: {
                        display: false,
                    },
                    grid: {
                        display: false,
                        borderWidth: 0,
                    },
                },
                y: {
                    ticks: {
                        display: false,
                    },
                    grid: {
                        display: false,
                        borderWidth: 0,
                    },
                },
            },
        });

        return { 
            basicOptions, 
            store: inject('store') 
        };
    },
    data() {
        return {
            surveys: [],
            activeIndex: 0,
            filters: {
                global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            },
        };
    },
    created() {
        let surveys = this.artifact.FunctionalAssessments.map((i: FunctionalAssessment) => i.Descriptor);

        surveys.push(
            ...this.artifact.PatientReportedItems.filter((i: PatientReported) => i.Survey.toLowerCase() !== 'na').map(
                (i: PatientReported) => i.Survey,
            ),
        );

        this.surveys = [...new Set(surveys)];
    },
    computed: {
        selectedSurvey() : FunctionalAssessment {
            this.store.AddLog(`Viewing survey ${this.surveys[this.activeIndex]}`);

            return this.artifact.FunctionalAssessments.filter(
                (i: FunctionalAssessment) =>
                    i.Descriptor.toLowerCase() === this.surveys[this.activeIndex].toLowerCase(),
            )[0];
        },
        surveyValues() {
            return this.selectedSurvey
                ? this.selectedSurvey.PatientResponses
                : this.artifact.PatientReportedItems.filter(
                      (i: PatientReported) => i.Survey.toLowerCase() === this.surveys[this.activeIndex].toLowerCase(),
                  );
        },
        scores() {
            return this.selectedSurvey?.FunctionalScoreSummaries;
        },
        chartData() {
            const scores = this.scores?.map((s: any) => s.FunctionalScore);
            let borderColor = '';
            let backgroundColor = '';

            if (scores.length > 0 && scores[0] > scores[scores.length - 1]) {
                borderColor = '#e91e63';
                backgroundColor = '#fef4f7';
            } else {
                borderColor = '#689f38';
                backgroundColor = '#f7faf5';
            }

            return {
                labels: this.scores?.map(() => ''),
                datasets: [
                    {
                        label: '',
                        type: 'shadowLine' as ChartType,
                        data: scores,
                        fill: false,
                        borderWidth: 4,
                        borderColor: borderColor,
                        backgroundColor: backgroundColor,
                    },
                ],
            };
        },
    },
    methods: {
        getPredictedAnswer(question: String) {
            const prediction = this.selectedSurvey.StagingInfo?.FunctionalityPredictions.filter(
                (fp: StagingFunctionalityPrediction) => fp.Question.toLowerCase() === question.toLowerCase(),
            );

            if (prediction && prediction.length > 0) return prediction[0].Answer;

            return '--';
        },
    },
});
