<template>
    <table class="p-datatable p-component p-datatable-sm">
        <thead class="p-datatable-thead" role="rowgroup">
            <tr role="row">
                <th role="cell" colspan="2">Functional Status Measures</th>
                <th role="cell" v-for="score in primaryAssessment.FunctionalScoreSummaries" :key="score.Label">
                    {{score.Label}} Score
                </th>
                <th role="cell" colspan="6">Interpretation of FS Scores/Stages Value</th>
            </tr>
        </thead>
        <tbody class="p-datatable-tbody" role="rowgroup">
            <tr class="" tabindex="0" role="row">
                <td role="cell" colspan="2">Patient's Physical FS Primary Measure</td>
                <td role="cell" v-for="score in primaryAssessment.FunctionalScoreSummaries" :key="score.Label">{{score.FunctionalScore}}</td>

                <td role="cell" colspan="6" v-if="!primaryAssessment.StagingInfo.CurrentStage && primaryAssessment.StagingInfo.IntakeStage.Stage !== 0">
                    Patient's intake functional measure is {{primaryAssessment.StagingInfo.IntakeStage.Measure}} out of 100 (higher number = greater function). This FS measure 
                    places the patient in Stage {{primaryAssessment.StagingInfo.IntakeStage.Stage}} and means the patient 
                    {{primaryAssessment.StagingInfo.StagingLevels[primaryAssessment.StagingInfo.IntakeStage.Stage - 1].Title.toLowerCase()}}
                </td>

                <td role="cell" colspan="6" v-else-if="primaryAssessment.StagingInfo.CurrentStage">
                    Patient's Intake FS Score was {{primaryAssessment.StagingInfo.IntakeStage.Measure}} initially placing the patient in Stage {{primaryAssessment.StagingInfo.IntakeStage.Stage}}. 
                    Patient's FS score now is {{primaryAssessment.StagingInfo.CurrentStage.Measure}} out of 100 ({{primaryAssessment.StagingInfo.CurrentStage.Measure - primaryAssessment.StagingInfo.IntakeStage.Measure}} 
                    points of functional change since intake), placing the patient in Stage {{primaryAssessment.StagingInfo.CurrentStage.Stage}} and 
                    means the {{primaryAssessment.StagingInfo.CurrentStage.PatientFunctionText}}
                </td>

                <td role="cell" colspan="6" v-else>
                    Patient's intake functional measure is {{primaryAssessment.StagingInfo.IntakeStage.Measure}} out of 100 (higher number = greater function).
                </td>
            </tr>													
            <tr class="" tabindex="0" role="row">
                <td role="cell" colspan="2">Risk Adjusted Statistical FOTO</td>
                <td role="cell">{{primaryAssessment.RiskAdjustedStatisticalFOTO}}</td>
                <td role="cell" v-for="score in primaryAssessment.FunctionalScoreSummaries.length - 1" :key="score"></td>
                <td role="cell" colspan="6">
                    Given the patient's risk-adjustment variables, like-patients nationally had a FS score of {{primaryAssessment.StagingInfo.RiskAdjustedStage.Measure}}, 
                    Stage {{primaryAssessment.StagingInfo.RiskAdjustedStage.Stage}}, at intake.
                </td>
            </tr>
        </tbody>
    </table>
</template>

<script lang="ts">
import { defineComponent } from '@vue/runtime-core';

export default defineComponent({
    name: 'FunctionalStatusMeasureTable',
    props: {
        primaryAssessment: Object,
    },
});
</script>
