<template>
    <form autocomplete="off" @submit.prevent="submit(!v$.$invalid)" class="p-fluid">
		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<Dropdown id="clinicianType" v-model="visit.ClinicianType" :options="options.clinicianType" optionLabel="display" optionValue="value" :class="{ 'p-invalid': submitted && v$.visit.ClinicianType.$invalid }" />
				<label for="clinicianType">Clinician Type</label>
			</span>
			<small v-if="(v$.visit.ClinicianType.$invalid && submitted) || v$.visit.ClinicianType.$pending.$response" class="p-error" >A clinician type is required.</small>
        </div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<Calendar id="visitDate" v-model="visit.VisitDate" :showIcon="true" :class="{ 'p-invalid': submitted && v$.visit.VisitDate.$invalid }"/>
				<label for="visitDate">Visit Date</label>
			</span>
			<small v-if="(v$.visit.VisitDate.$invalid && submitted) || v$.visit.VisitDate.$pending.$response" class="p-error">A visit date is required.</small>
        </div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<InputNumber showButtons id="minutesSpent" v-model="visit.MinutesSpent" suffix=" minutes" :min="1" :class="{ 'p-invalid': submitted && v$.visit.MinutesSpent.$invalid }" />
				<label for="minutesSpent">Length</label>
			</span>
			<small v-if="(v$.visit.MinutesSpent.$invalid && submitted) || v$.visit.MinutesSpent.$pending.$response" class="p-error" >A visit length is required.</small>
        </div>

		<div class="p-field p-col-12 p-md-12">
			<span class="p-float-label">
				<InputNumber showButtons id="revenues" v-model="visit.Revenue" mode="currency" currency="USD" locale="en-US" :min="0" :class="{ 'p-invalid': submitted && v$.visit.Revenue.$invalid }" />
				<label for="revenue">Revenue</label>
			</span>
			<small v-if="(v$.visit.Revenue.$invalid && submitted) || v$.visit.Revenue.$pending.$response" class="p-error">A revenue value is required.</small>
        </div>

		<Divider type="dashed"></Divider>

		<div class="p-grid">
			<div class="p-col-12 p-md-8"></div>
			<div class="p-col-12 p-md-4">
				<Button type="submit" label="Submit" :loading="loading" icon="pi pi-check" class="" />
			</div>
		</div>
    </form>
</template>

<script lang="ts">
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import ApplicationStore from '../../stores/applicationStore';
import Visit from '../../models/foto/visit';

export default defineComponent({
    name: 'ArtifactVisitCreationForm',
    setup: function(): any {
        return {
            store: inject('store') as ApplicationStore,
            v$: useVuelidate(),
            options: {
                clinicianType: ['ATC', 'CCCSLP', 'COTA', 'DC', 'DPT', 'EP', 'LPN', 'MPT', 'MSPT', 'NA', 'OT', 'Other', 
                    'OTR', 'PhD', 'PT', 'PTA', 'RN', 'SLP', 'SPT', 'ST', 'RT'].map(i =>{ return { display: i, value: i }}),
            },
        };
    },
    props: {
        externalEpisodeId: String,
    },
    data: function(): any {
        return {
            visit: new Visit() as Visit,
            loading: false,
            submitted: false,
        };
    },
    methods: {
        async submit() {
            this.submitted = true;

            try{
                if (!this.v$.visit.$invalid) {
                    this.loading = true;
                    this.$Progress.start();
    
                    this.store.AddLog(`Adding visit to episode with external id of ${this.externalEpisodeId}`, this.visit);
                    this.visit.ExternalEpisodeId = this.externalEpisodeId;
                    await this.store.Episode.AddVisit(this.visit);
    
                    this.$emit('close');
                    this.$Progress.finish();
                    this.loading = false;
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
    },
    validations() {
        return {
            visit: {
                ClinicianType: {
                    required,
                },
                MinutesSpent: {
                    required,
                },
                Revenue: {
                    required,
                },
                VisitDate: {
                    required,
                },
            },
        };
    },
});
</script>
