<template>
    <div>
		<vue-progress-bar />
        <Toast />
        <transition name="fade" mode="out-in">
            <div key="1" class="loader" v-if="layout.loading">
                <AppSplash :size="150" />
            </div>
            <div key="2" :class="containerClass()" @click="onWrapperClick" v-else>
                <AppTopBar />
                <Sidebar v-model:visible="visibleRight" :baseZIndex="1000" position="right">
                    <h3>Right Sidebar</h3>
                </Sidebar>
				<transition name="layout-sidebar">
                    <AppSidebar v-show="isSidebarVisible()" />
				</transition>
				<div class="layout-main">
                    <transition name="fade" mode="out-in">
                        <div key="1a" v-if="store.Episode.State.Episodes?.length !== 0">
                            <Dialog header="Launch Survey" :dismissableMask="true" v-model:visible="showSurvey" :style="{width: '300px'}" :modal="true" class="p-fluid dialog-head-p-b-small">
                                <SurveyLaunch :episodeId="selectedId" />
                            </Dialog>
                            
                            <Dialog v-model:visible="showEpisodeForm" :dismissableMask="true" :style="{ width: '50rem' }" header="Episode Details" :modal="true" class="p-fluid" >
                                <EpisodeCreationForm @close="showEpisodeForm = false;" />
                            </Dialog>

                            <Dialog v-model:visible="showVisitForm" :dismissableMask="true" :style="{ width: '25rem' }" header="Visit Details" :modal="true" class="p-fluid" >
                                <ArtifactVisitCreationForm @close="showVisitForm = false;" :externalEpisodeId="selectedId" />
                            </Dialog>

                            <Dialog v-model:visible="showDischargeForm" :dismissableMask="true" :style="{ width: '25rem' }" header="Discharge Episode" :modal="true" class="p-fluid" >
                                <EpisodeDischargeForm @close="showDischargeForm = false;" :externalEpisodeId="selectedId" />
                            </Dialog>

                            <Dialog v-model:visible="showClaimEpisodeForm" :dismissableMask="true" :style="{ width: '75rem' }" header="Claim Episode" :modal="true" class="p-fluid" >
                                <ClaimEpisodeForm :episodeId="selectedId"  @close="showClaimEpisodeForm = false;" />
                            </Dialog>

                            <AppDash :activeEpisodeFilters='layout.activeEpisodeFilters' :showDischarged='layout.showDischargedEpisodes' @CardToggle='toggleCardEpisodeDisplay' @ToggleDischargedEpisodes='toggleDischargeView' @DisplaySurvey='displaySurveyModal' @DisplayDischarge='displayDischargeModal' @DisplayVisit='displayVisitModal' @DisplayEpisodeClaim='displayEpisodeClaimModal' />
                        </div>
                        <div key="1b" class="p-col-12 flex ta-c align-items-center justify-content-center flex-d-c noEpisodesContainer" v-else-if="store.Fhir.State.Context.LaunchedByResource === 'Practitioner'">
                            <div class='p-col-12'>
                                <h1 class="bolder ta-c">No FOTO episodes were found for patient, {{outboundPatient.Name}}. Would you like to create one?</h1>
                            </div>
                            <div class="p-col-12">
                                <br />
                                <Button class="important-button p-shadow-24" type="button" label="Create an Episode" @click="showEpisodeForm = true" />
                            </div>

                            <Dialog v-model:visible="showEpisodeForm" :dismissableMask="true" :style="{ width: '50rem' }" header="Episode Details" :modal="true" class="p-fluid" >
                                <EpisodeCreationForm @close="showEpisodeForm = false;" />
                            </Dialog>
                        </div>
                        <div key="1c" class="p-col-12 flex ta-c align-items-center justify-content-center flex-d-c noEpisodesContainer" v-else>
                            <div class='p-col-12'>
                                <h1 class="bolder ta-c">No episodes were found.</h1>
                            </div>
                        </div>
                    </transition>
                    <div class="layout-footer wrap">
                        <img alt="Logo" src="../assets/nh_brand_symbol.png" height="30" class="m-r-_5-r" />
                        <span class="ls-_5"><span class="fs-n ls-n bolder color-primary">Net Health</span> - Reuniting caregivers with their calling.</span>
                    </div>
				</div>
                <SpeedDial :tooltipOptions="{position: 'left'}" :model="speedDialItems" style="position:fixed; bottom:3rem; right:3rem; z-index: 1000;" direction="up-left" :transitionDelay="80" type="quarter-circle" buttonClass="p-shadow-24 main-speeddial" />
            </div>
        </transition>
    </div>
</template>

<script lang="ts">
import { defineComponent, inject, reactive } from '@vue/runtime-core';
import AppSplash from '../components/AppSplash.vue';
import AppDash from '../components/AppDash.vue';
import AppTopBar from '../components/AppTopBar.vue';
import AppSidebar from '../components/AppSidebar.vue';
import Layout from '../models/layout';
import SurveyLaunch from '../components/form/SurveyLaunch.vue';
import EpisodeCreationForm from '../components/form/EpisodeCreationForm.vue';
import ArtifactVisitCreationForm from '../components/form/ArtifactVisitCreationForm.vue';
import EpisodeDischargeForm from '../components/form/EpisodeDischargeForm.vue';
import ClaimEpisodeForm from '../components/form/ClaimEpisodeForm.vue';

export default defineComponent({
    name: 'App',
    components: {
        SurveyLaunch,
        EpisodeCreationForm,
        ArtifactVisitCreationForm,
        EpisodeDischargeForm,
        ClaimEpisodeForm,
        AppSplash,
        AppTopBar,
        AppDash,
        AppSidebar,
    },
    setup() {
        const store = inject('store');
        const fhirClient = inject('fhirclient');
        const layout = reactive(new Layout((fhirClient as any)?.user?.id));

        return {
            fhirClient,
            store,
            layout
        };
    },
    data: function(): any {
        return {
            selectedId: null,
            outboundPatient: null,
            showClaimEpisodeForm: false,
            showSurvey: false,
            showDischargeForm: false,
            showVisitForm: false,
            showEpisodeForm: false,
        };
    },
    async created() {
        try {
            this.$Progress.start();
            (Window as any).$toast = this.$toast;
            (Window as any).$progress = this.$Progress;

            const started = performance.now();
            await this.store.InitAsync(this.fhirClient);
            const stopped = performance.now();

            this.outboundPatient = this.store.GetContextValue('FotoPatient');
            this.removeLoaderAfterElapsed(stopped - started);
        } catch (e) {
            Promise.reject(e);
        }
    },
    provide() {
        return {
            store: this.store,
        };
    },
    computed: {
        speedDialItems: function(): Array<object> {
            const items = [
                {
                    label: this.layout.mode === 'static' ? 'Hide Sidebar' : 'Show Sidebar',
                    icon: this.layout.mode === 'static' ? 'pi pi-align-justify' : 'pi pi-align-right',
                    command: () => {
                        this.store.AddLog(`Toggling layout mode to ${this.layout.colorMode === 'static' ? 'overlay' : 'static'}`);
                        this.layout.toggleMode()
                    }
                },
                {
                    label: this.layout.colorMode === 'light' ? 'Dark Mode' : 'Light Mode',
                    icon: this.layout.colorMode === 'light' ? 'pi pi-moon' : 'pi pi-sun',
                    command: () => {
                        this.layout.loading = true;
                        this.store.AddLog(`Toggling color mode to ${this.layout.colorMode === 'light' ? 'dark' : 'light'}`);
                        setTimeout(() => this.layout.toggleColorMode(), 500)
                        setTimeout(() => {
                            this.layout.loading = false;
                        }, 2500);
                    }
                },
                {
                    label: 'Refresh Data',
                    icon: 'pi pi-refresh',
                    command: async () => { 
                        this.layout.loading = true;
                        this.store.AddLog('Refreshing data');
                        
                        const started = performance.now();
                        this.store = await this.store.Refresh(this.fhirClient);
                        const stopped = performance.now();

                        this.removeLoaderAfterElapsed(stopped - started);
                    },
                },
            ];
                    
            if(this.store.Fhir.State.Context.LaunchedByResource === "Practitioner"){
                items.unshift({
                    label: 'Create Episode',
                    icon: 'pi pi-plus-circle',
                    command: () => {
                        this.showEpisodeForm = true
                        this.store.AddLog('Displaying episode creation form.');
                    }
                });
            }

            return items;
        },
    },
    methods: {
        removeLoaderAfterElapsed(elapsed: number){
            if(elapsed < 2500){
                setTimeout(() => {
                    this.$Progress.finish();
                    this.layout.loading = false;                    
                }, 2500 - elapsed)
            }
            else{
                this.$Progress.finish();
                this.layout.loading = false;
            }
            setTimeout(() => {
                this.layout.loading = false;
            }, 2500);
        },
        toggleDischargeView(){
            this.layout.toggleDischargeView();
        },
        toggleCardEpisodeDisplay(){
            this.layout.onCardToggle();
        },
        displayVisitModal(externalEpisodeId: string) {
            this.store.AddLog(`Displaying visit modal for episode with external id ${externalEpisodeId}`);
            this.selectedId = externalEpisodeId;
            this.showVisitForm = true;
        },
        displayEpisodeClaimModal(episodeId: number) {
            this.store.AddLog(`Displaying claim modal for episode with id ${episodeId}`);
            this.selectedId = episodeId;
            this.showClaimEpisodeForm = true;
        },
        displayDischargeModal(externalEpisodeId: string) {
            this.store.AddLog(`Displaying discharge modal for episode with external id ${externalEpisodeId}`);
            this.selectedId = externalEpisodeId;
            this.showDischargeForm = true;
        },
        displaySurveyModal(slotProps: any) {
            this.store.AddLog(`Displaying survey modal for episode with id ${slotProps.EpisodeId}`);
            this.selectedId = slotProps.EpisodeId;
            this.showSurvey = true;
        },
        isSidebarVisible() {
            if (window.innerWidth > 1024) {
                if (this.layout.mode === 'static') return !this.layout.staticMenuInactive;
                else if (this.layout.mode === 'overlay') return this.layout.overlayMenuActive;
                else return true;
            } else return true;
        },
        containerClass: function(): Array<Object> {
            return [
                'layout-wrapper',
                {
                    'layout-overlay': this.layout.mode === 'overlay',
                    'layout-static': this.layout.mode === 'static',
                    'layout-static-sidebar-inactive': this.layout.staticMenuInactive && this.layout.mode === 'static',
                    'layout-overlay-sidebar-active': this.layout.overlayMenuActive && this.layout.mode === 'overlay',
                    'layout-mobile-sidebar-active': this.layout.mobileMenuActive,
                },
            ];
        },
    },
});
</script>
