
import Hl7Service from '../../services/hl7/hl7Service';
import { defineComponent } from '@vue/runtime-core';
import { PatientReportType } from '../../models/foto/patientReportType';
import { inject } from 'vue';
import AdditionalItemTable from './AdditionalItemTable.vue';
import Artifact from '../../models/foto/artifact';
import ArtifactSummaryChart from './ArtifactSummaryChart.vue';
import Episode from '../../models/foto/episode';
import FunctionalStatusMeasureTable from './FunctionalStatusMeasureTable.vue';
import ArtifactSurveys from './ArtifactSurveys.vue';
import RiskAdjustmentCriteria from './RiskAdjustmentCriteria.vue';
import Patient from '../../models/foto/patient';
import { FunctionalAssessment } from '../../models/foto/functionalAssessment';
import { Identifier } from 'fhir/r4';

export default defineComponent({
    name: 'ArtifactDetail',
    components: {
        AdditionalItemTable,
        ArtifactSummaryChart,
        FunctionalStatusMeasureTable,
        ArtifactSurveys,
        RiskAdjustmentCriteria,
    },
    setup: function(): any {
        const store = inject('store');
        const patient = (store as any).GetContextValue('FotoPatient');

        return {
            store,
            patient,
        };
    },
    props: {
        episode: Episode,
    },
    data: function(): any {
        return {
            error: null,
            artifacts: [],
            inContextArtifact: null,
            artifactVisitValue: 0,
            loadingDetails: false,
            updatingVisitNumber: false,
        };
    },
    async created() {
        this.$Progress.start();
        this.loadingDetails = true;

        try{
            this.artifacts = await this.store.Artifact.ForEpisode(this.episode.ExternalEpisodeId);
            this.inContextArtifact = this.artifacts[0];
            this.store.SetContextValue('SelectedArtifact', this.inContextArtifact);
        }
        catch(ex: any){
            this.$emit('Error', ex);
            this.error = ex;
            Promise.reject(ex);
        }

        this.$Progress.finish();
        this.loadingDetails = false;
    },
    computed: {
        panelBodyHeader(){
            let header = `${this.getformattedDate(this.inContextArtifact.SurveyDateTimeCompleted)} - ${PatientReportType[this.inContextArtifact.ReportType].toString()} Summary - Completed`;

            if([0, 1].includes(this.inContextArtifact.ReportType) && this.inContextArtifact.FunctionalAssessments.length > 0){
                const primaryMeasureAssessment = this.inContextArtifact.FunctionalAssessments.filter((fa: FunctionalAssessment) => fa.Descriptor.toLowerCase() === 'physical fs primary measure')[0];

                if (primaryMeasureAssessment) {
                    const scoreArrLen = primaryMeasureAssessment.FunctionalScoreSummaries?.length;
                    header += ` - ${primaryMeasureAssessment?.FunctionalScoreSummaries[scoreArrLen - 1]?.FunctionalScore}`;
                } else header += ' - N/A';
            }

            return header;
        },
        primaryAssessment(){
            if (this.inContextArtifact.FunctionalAssessments.length > 0)
                return this.inContextArtifact.FunctionalAssessments.filter((fa: FunctionalAssessment) => fa.Descriptor.toLowerCase() === 'physical fs primary measure')[0];
            else
                return null;
        },
        menuItems(){
            const items = [
                {
                    label: 'View',
                    icon: 'pi pi-eye',
                    command: async () => { await this.viewReport() },
                },
                {
                    label: 'Download',
                    icon: 'pi pi-file-pdf',
                    command: async () => { await this.downloadReport() },
                }
            ];

            if(this.store.Fhir.State.Context.LaunchedByResource === 'Practitioner')
                items.unshift({
                    label: 'Upload to MyChart',
                    icon: 'pi pi-cloud-upload',
                    command: async () => { await this.uploadReport() },
                });

            return items;
        }
    },
    methods: {
        createPanelHeader(artifact: Artifact){
            return `${this.getformattedDate(artifact.SurveyDateTimeCompleted)} Functional ${PatientReportType[artifact.ReportType].toString()} Summary`
        },
        artifactSelected(event: any){
            this.inContextArtifact = this.artifacts[event.index];
            this.store.SetContextValue('SelectedArtifact', this.inContextArtifact);
        },
        toggleArtifactVisitNumOp(event: any) {
            this.$refs[`artifactVisitNumOp-${this.inContextArtifact.ReportId}`].toggle(event);
        },
        async launchSurvey() {
            try{
                this.store.AddLog(`Launching survey for episode ${this.episode.EpisodeId}`);
                const surveyDetails = await this.store.Episode.GetSurvey(this.episode.EpisodeId);
                window.open(surveyDetails.Url);
            }
            catch(e){
                Promise.reject(e);
            }
        },
        async updateVisitNumber(event: any) {
            try{
                const artifact = this.artifacts.filter((a: Artifact) => a.ReportId === this.inContextArtifact.ReportId)[0];
    
                if (artifact.Visit !== this.artifactVisitValue){
                    this.store.AddLog(`Updating visit for report ${this.inContextArtifact.ReportId} with value ${this.artifactVisitValue}`);
                    this.updatingVisitNumber = true;
                    this.$Progress.start();

                    await this.store.Artifact.SetVisit(this.inContextArtifact.ReportId, this.artifactVisitValue);
    
                    this.$Progress.finish();
                    this.updatingVisitNumber = false;
                }
                    
                this.$refs[`artifactVisitNumOp-${this.inContextArtifact.ReportId}`].toggle(event);
            }
            catch(e){
                Promise.reject(e);
            }
        },
        async uploadReport() {
            try{
                this.store.AddLog('Uploading report to mychart');

                this.$Progress.start();
                const report = await this.store.Artifact.GetReport(this.inContextArtifact.ReportId);
                const patient = this.store.Fhir.State.Context.Patient;
                const encounter = this.store.Fhir.State.Context.Encounter;
                const patientId = patient.identifier.filter((i: Identifier) => i.system.endsWith('114350.1.1'))[0]?.value;
                const pFName = patient.name[0]?.given[0];
                const pLName = patient.name[0]?.family;
                const encounterId = encounter.identifier.filter((i: Identifier) => i.system.endsWith('698084.8'))[0]?.value;
                const externalId = `${this.inContextArtifact.ReportId}_${PatientReportType[this.inContextArtifact.ReportType].toLowerCase()}`;
                const base64 = Buffer.from(report).toString('base64');
                const message = Hl7Service.Mdm.Build(patientId, pFName, pLName, encounterId, externalId, base64);
    
                this.store.AddLog('HL7 message for report', message);

                await Hl7Service.Mdm.Send(message);
                this.$Progress.finish();
            }
            catch(e){
                Promise.reject(e);
            }
        },
        async downloadReport() {
            try{
                this.store.AddLog(`Downloading report ${this.inContextArtifact.ReportId}`);

                this.$Progress.start();
                const report = await this.store.Artifact.GetReport(this.inContextArtifact.ReportId);
                const blob = new Blob([new Uint8Array(report)], { type: 'application/pdf' });
                const link = document.createElement('a');
                const reportType = PatientReportType[this.inContextArtifact.ReportType];
                const pat: Patient = this.store.GetContextValue('FotoPatient') as Patient;
                const dateCompleted = new Date(this.inContextArtifact.SurveyDateTimeCompleted);
                const dateStarted = new Date(this.inContextArtifact.SurveyDateTimeStarted);
                const dateStr = dateCompleted.getFullYear() !== 1 
                    ? `${dateCompleted.getMonth() + 1}${dateCompleted.getDate()}${dateCompleted.getFullYear()}`
                    : `${dateStarted.getMonth() + 1}${dateStarted.getDate()}${dateStarted.getFullYear()}`;
    
                link.href = URL.createObjectURL(blob);
                link.download = `${pat.Alias}_${reportType}_${dateStr}.pdf`;
                link.click();
                this.$Progress.finish();
            }
            catch(e){
                Promise.reject(e);
            }
        },
        async viewReport() {
            try{
                this.store.AddLog(`Viewing report ${this.inContextArtifact.ReportId}`);
                this.$Progress.start();
                const report = await this.store.Artifact.GetReport(this.inContextArtifact.ReportId);
                const blob = new Blob([new Uint8Array(report)], { type: 'application/pdf' });
                const tab = window.open('about:blank');
    
                tab.location = URL.createObjectURL(blob) as any;
                tab.focus();
                this.$Progress.finish();
            }
            catch(e){
                Promise.reject(e);
            }
        },
        toggle(event: any) {
            const ref: any = this.$refs[`artifact_report_menu_${this.inContextArtifact.ReportId}`];
            ref.toggle(event);
            event.stopPropagation();
        },
        getformattedDate(datetime: string) {
            return new Date(datetime).toISOString().split('T')[0].replace(/-/g, '/');
        },
    },
});
