import * as R4 from 'fhir/r4';

export default class LaunchContext {
    Patient: R4.Patient | null;
    Location: R4.Location | null;
    Encounter: R4.Encounter | null;
    EpisodeOfCare: R4.EpisodeOfCare | null;
    Practitioner: R4.Practitioner | null;
    LaunchedByResource: String | null;
}