
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { defineComponent } from '@vue/runtime-core';
import { inject } from 'vue';
import ApplicationStore from '../../stores/applicationStore';
import Visit from '../../models/foto/visit';

export default defineComponent({
    name: 'ArtifactVisitCreationForm',
    setup: function(): any {
        return {
            store: inject('store') as ApplicationStore,
            v$: useVuelidate(),
            options: {
                clinicianType: ['ATC', 'CCCSLP', 'COTA', 'DC', 'DPT', 'EP', 'LPN', 'MPT', 'MSPT', 'NA', 'OT', 'Other', 
                    'OTR', 'PhD', 'PT', 'PTA', 'RN', 'SLP', 'SPT', 'ST', 'RT'].map(i =>{ return { display: i, value: i }}),
            },
        };
    },
    props: {
        externalEpisodeId: String,
    },
    data: function(): any {
        return {
            visit: new Visit() as Visit,
            loading: false,
            submitted: false,
        };
    },
    methods: {
        async submit() {
            this.submitted = true;

            try{
                if (!this.v$.visit.$invalid) {
                    this.loading = true;
                    this.$Progress.start();
    
                    this.store.AddLog(`Adding visit to episode with external id of ${this.externalEpisodeId}`, this.visit);
                    this.visit.ExternalEpisodeId = this.externalEpisodeId;
                    await this.store.Episode.AddVisit(this.visit);
    
                    this.$emit('close');
                    this.$Progress.finish();
                    this.loading = false;
                }
            }
            catch(er){
                Promise.reject(er);
            }
        },
    },
    validations() {
        return {
            visit: {
                ClinicianType: {
                    required,
                },
                MinutesSpent: {
                    required,
                },
                Revenue: {
                    required,
                },
                VisitDate: {
                    required,
                },
            },
        };
    },
});
