/* eslint-disable */
import {LineController} from 'chart.js';

export default class ShadowLine extends LineController {
  draw() {
    const chart = this.chart;
    const ctx = chart.ctx;
    const _stroke = ctx.stroke;

    ctx.stroke = function() {
        ctx.save();
        ctx.shadowColor = ctx.strokeStyle as string;
        ctx.shadowBlur = 7;
        ctx.shadowOffsetX = 0;
        ctx.shadowOffsetY = 3;
        _stroke.apply(this, arguments as any);
        ctx.restore();
    };

    super.draw.apply(this, arguments as any);
    ctx.stroke = _stroke;
  }
}
ShadowLine.id = 'shadowLine';