
import { defineComponent } from '@vue/runtime-core';
import Artifact from '../../models/foto/artifact';
import { RiskAdjustedCriteria } from '../../models/foto/riskAdjustedCriteria';

export default defineComponent({
    name: 'RiskAdjustmentCriteria',
    props: {
        artifact: Object,
    },
    mounted() {
        const artifact: Artifact = this.artifact as Artifact;
        const tBody = document.querySelector(`#rai-tbody-${artifact.ReportId}`);
        let value = '<tr colspan="12">';
        let num = 1;

        artifact.RiskAdjustedItems.forEach((item: RiskAdjustedCriteria) => {
            if (num % 6 === 0) {
                value += `<td role="cell" colspan="6">${item.Attribute}: ${item.Value}</td>`;
                value += `</tr>`;
                if (num !== artifact.RiskAdjustedItems.length) value += '<tr colspan="12" class="">';
            } else {
                value += `<td role="cell" colspan="6">${item.Attribute}: ${item.Value}</td>`;

                if (num === artifact.RiskAdjustedItems.length && num % 2 !== 0)
                    value += `<td role="cell" colspan="6"></td>`;
            }
            num++;
        });

        tBody.innerHTML = value;
    },
});
