<template>
    <div class="p-grid p-fluid dashboard">
		<div :class="`p-col-12 statusCard inProgress ${d_activeEpisodeFilters.includes(0) ? 'active' : ''}`" @click="toggleCard(0)">
			<Card>
				<template #content>
					<div class="flex p-jc-between m-b-1-r">
						<div>
							<div class="fw-600 text-xl">In Progress</div>
						</div>
						<div class="flex align-items-center p-jc-center bg-green-100 border-round" style="width:2.5rem;height:2.5rem;">
							<span class="bolder text-green-500 text-xl">{{episodesWithStatusCount(0)}}</span>
						</div>
					</div>
					<span class="block text-500 font-medium">Open Episodes</span>
				</template>
			</Card>
		</div>
		<div :class="`p-col-12 statusCard intakeOverdue ${d_activeEpisodeFilters.includes(1) ? 'active' : ''}`" @click="toggleCard(1)">
			<Card>
				<template #content>
					<div class="flex p-jc-between m-b-1-r">
						<div>
							<div class="fw-600 text-xl">Intake Overdue</div>
						</div>
						<div class="flex align-items-center p-jc-center bg-blue-100 border-round" style="width:2.5rem;height:2.5rem;">
							<span class="bolder text-blue-500 text-xl">{{episodesWithStatusCount(1)}}</span>
						</div>
					</div>
					<span class="block text-500 font-medium">7 to 14 days of inactivity</span>
				</template>
			</Card>
		</div>
		<div :class="`p-col-12 statusCard statusOverdue ${d_activeEpisodeFilters.includes(2) ? 'active' : ''}`" @click="toggleCard(2)">
			<Card>
				<template #content>
					<div class="flex p-jc-between m-b-1-r">
						<div>
							<div class="fw-600 text-xl">Status Overdue</div>
						</div>
						<div class="flex align-items-center p-jc-center bg-purple-100 border-round" style="width:2.5rem;height:2.5rem;">
							<span class="bolder text-purple-500 text-xl">{{episodesWithStatusCount(2)}}</span>
						</div>
					</div>
					<span class="block text-500 font-medium">14 to 30 days of inactivity</span>
				</template>
			</Card>
		</div>
		<div :class="`p-col-12 statusCard close ${d_activeEpisodeFilters.includes(3) ? 'active' : ''}`" @click="toggleCard(3)">
			<Card>
				<template #content>
					<div class="flex p-jc-between m-b-1-r">
						<div>
							<div class="fw-600 text-xl">Close</div>
						</div>
						<div class="flex align-items-center p-jc-center bg-orange-100 border-round" style="width:2.5rem;height:2.5rem;">
							<span class="bolder text-orange-500 text-xl">{{episodesWithStatusCount(3)}}</span>
						</div>
					</div>
					<span class="block text-500 font-medium">30 to 44 days of inactivity</span>
				</template>
			</Card>
		</div>
		<div :class="`p-col-12 statusCard inactive ${d_activeEpisodeFilters.includes(4) ? 'active' : ''}`" @click="toggleCard(4)">
			<Card>
				<template #content>
					<div class="flex p-jc-between m-b-1-r">
						<div>
							<div class="fw-600 text-xl">Inactive</div>
						</div>
						<div class="flex align-items-center p-jc-center bg-pink-100 border-round" style="width:2.5rem;height:2.5rem;">
							<span class="bolder text-pink-600 text-xl">{{episodesWithStatusCount(4)}}</span>
						</div>
					</div>
					<span class="block text-500 font-medium">45+ days of inactivity</span>
				</template>
			</Card>
		</div>

        <div class="p-col-12 p-lg-12 dashboard-main">
			<Card>
				<template #title>
                    <div class="table-header p-d-flex p-flex-column p-flex-md-row p-jc-md-between">
						<h5 class="p-mb-2 p-m-md-0 p-as-md-center">Patient Episodes</h5>
						<InputSwitch @change="$emit('ToggleDischargedEpisodes')" v-model="d_showDischarged" id="showDischarged" v-tooltip.top="'Show Discharged'" />
					</div>
				</template>
				<template #content>
					<DataTable ref="dt" :autoLayout='true' scrollHeight='flex' paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown" :rowsPerPageOptions="[5,10,15]" currentPageReportTemplate="Showing {first} to {last} of {totalRecords}" :value="episodes" @rowExpand="onRowExpand" v-model:expandedRows="expandedRows" dataKey="EpisodeId" class="episode-list-dt" responsiveLayout="stack" :paginator="true" :rows="5" :filters="filters">
						<template #empty>No episodes found</template>
						
						<Column :expander="true" />
						<Column field="TherapistName" header="Therapist" :sortable="true"></Column>
						<Column field="BodyPartText" header="Condition" :sortable="true"></Column>
						<Column field="ImpairmentText" header="Impairment" :sortable="true"></Column>
						<Column field="CreateDate" header="Setup" :sortable="true">
							<template #body="slotProps">
								<span>{{formatDateTime(slotProps.data.CreateDate)}}</span>
							</template>
						</Column>
						<Column field="LatestIntakeTime" header="Intake" :sortable="true">
							<template #body="slotProps">
								<span>{{formatDateTime(slotProps.data.LatestIntakeTime)}}</span>
							</template>
						</Column>
						<Column field="LatestStatusTime" header="Status" :sortable="true">
							<template #body="slotProps">
								<span>{{formatDateTime(slotProps.data.LatestStatusTime)}}</span>							
							</template>
						</Column>
						<Column field="DischargeTime" header="Discharge" :sortable="true">
							<template #body="slotProps">
								<span>{{formatDateTime(slotProps.data.DischargeTime)}}</span>							
							</template>
						</Column>
						<Column field="Info" header="Info" class="info-column" :sortable="true">
							<template #body="slotProps">
								<Tag v-if="slotProps.data.Info === 0" class="bg-green-100 text-green-500" severity="success" value="In Progress" ></Tag>
								<Tag v-else-if="slotProps.data.Info === 1" class="bg-blue-100 text-blue-500" severity="danger" value="Intake Overdue" ></Tag>
								<Tag v-else-if="slotProps.data.Info === 2" class="bg-purple-100 text-purple-500" severity="warning" value="Status Overdue" />
								<Tag v-else-if="slotProps.data.Info === 3" class="bg-orange-100 text-orange-500" severity="danger" value="Close" />
								<Tag v-else-if="slotProps.data.Info === 4" class="bg-pink-100 text-pink-600" severity="danger" value="Inactive" />
								<Tag v-else-if="slotProps.data.Info === 5" class="bg-bluegray-100 text-bluegray-500" severity="danger" :value="`Discharged${slotProps.data.NPId && slotProps.data.NPId > 0 ? ' - NP' : ''}`" />
							</template>
						</Column>

						<template #expansion="slotProps">
							<template v-if="slotProps.data.ExternalEpisodeId">
								<template v-if="!error">
									<div class="p-grid m-t-_5-r">
										<div class="p-lg-10">
											<h3 class="m-b-_5-r m-t-0">
												{{generateEpisodeDetailHeader(slotProps.data)}} 
												<span v-if="store.Fhir.State.Context.LaunchedByResource === 'Practitioner'" @click="toggleExtEpisodeIdOp" class="toggleOp color-primary bolder" v-tooltip.top="'External ID (Click to edit)'"> - {{slotProps.data.ExternalEpisodeId}}</span>
											</h3>
										</div>
										<div class="p-lg-2" v-if="slotProps.data.Info !== 5">
											<Button icon="pi pi-cog" class="w-auto float-r p-text-bold p-button-sm" @click="toggle" type="button" label="Episode Options" aria-haspopup="true" :aria-controls="'episode_option_menu_' + slotProps.data.EpisodeId"/>
											<Menu class="w-auto" :id="'episode_option_menu_' + slotProps.data.EpisodeId" :ref="'episode_option_menu_' + slotProps.data.EpisodeId" :model="menuItems" :popup="true" />
										</div>
									</div>

									<OverlayPanel :showCloseIcon="true" :dismissable="true" class="artifact-detail-op-md" ref="extEpisodeIdOp">
										<div class="p-col-12">
											<h3 class="m-t-0">Update External Episode ID</h3>
											<span class="importantMessage p-confirm-popup-message">Editing or removing this ID may cause issues with your EMR integration. Clear the ID field to release the episode from FOTO.</span>
											<Divider type="dashed"></Divider>
										</div>

										<div class="p-col-12">
											<form autocomplete="off" @submit.prevent="submitExternalEpisodeIdEdit" class="p-fluid p-grid">
												<div class="p-field p-col-12 p-md-12">
													<span class="p-float-label">
														<Dropdown id="externalEpisodeId-select-op" v-model="inContextExternalIdOption" :options="episodesOfCare" optionLabel="active">
															<template #value="epiProps">
																<div v-if="epiProps.value">
																	{{epiProps.value.period.start}} {{epiProps.value.period.end ? ' - ' + epiProps.value.period.end : ''}} {{epiProps.value.type[0]?.text}}
																</div>
																<div v-else>&nbsp;</div>
															</template>
															<template #option="epiProps">
																<div>{{epiProps.option.period.start}} {{epiProps.option.period.end ? ' - ' + epiProps.option.period.end : ''}} {{epiProps.option.type[0]?.text}}</div>
															</template>
														</Dropdown>
														<label for="externalEpisodeId-select-op">Episode</label>
													</span>
												</div>

												<div class="p-col-6 p-md-6" />
												<div class="p-col-6 p-md-6">
													<Button type="submit" label="Submit" :loading="updatingExternalId" icon="pi pi-check" class="" />
												</div>
											</form>
										</div>
									</OverlayPanel>
								</template>
								<div class="p-col-12">
									<ArtifactDetail :episode="slotProps.data" @Error='errorEvent' />
								</div>
							</template>
							<template v-else-if="store.Fhir.State.Context.LaunchedByResource === 'Practitioner'">
								<div class='p-col-12'>
									<h2 class="ta-c bolder">This episode has not been claimed in FOTO. Would you like to claim it?</h2>
								</div>
								<div class="p-col-12">
									<br />
									<Button class="important-button p-shadow-24" type="button" label="Claim Episode" @click="$emit('DisplayEpisodeClaim', selectedEpisode.EpisodeId)" />
									<br />
								</div>
							</template>
							<template v-else>
								<div class='p-col-12'>
									<h2 class="ta-c bolder">No data for this episode was found.</h2>
								</div>
							</template>
						</template>
					</DataTable>
				</template>
			</Card>
		</div>
    </div>
</template>

<script lang="ts">
import { inject } from 'vue';
import { defineComponent } from '@vue/runtime-core';
import { FilterMatchMode } from 'primevue/api';
import ArtifactDetail from './artifact/ArtifactDetail.vue';
import Episode from '../models/foto/episode';
import EpisodeInfo from '../models/foto/episodeInfo';
import { EpisodeOfCare } from 'fhir/r4';

export default defineComponent({
    name: 'AppDash',
    components: {
        ArtifactDetail,
    },
    setup: function(): any {
        const store = inject('store');
        const patient = (store as any).GetContextValue('FotoPatient');
		const episodesOfCare = (store as any).Fhir.State.Context.EpisodeOfCare;

        return {
            store,
            patient,
			episodesOfCare
        };
    },
	props: {
		showDischarged: Boolean,
		activeEpisodeFilters: Array
	},
    data: function(): object {
        return {
			error: null,
			d_showDischarged: this.showDischarged,
			updatingExternalId: false,
			inContextExternalIdOption: null,
            selectedEpisode: null,
			expandedRows: [],
            d_activeEpisodeFilters: this.activeEpisodeFilters,
            filters: {
                global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            },
        };
    },
    computed: {
        episodes: function(): Episode {
            return this.store.Episode.FilterLocal(
                (e: Episode) =>
                    this.d_activeEpisodeFilters.includes(e.Info) ||
                    (this.d_showDischarged && e.Info === EpisodeInfo.Discharged),
            );
        },
		menuItems: function(): Array<Object>{
			const items = [
				{
					label: 'View Survey',
                    icon: 'pi pi-file',
                    command: this.displaySurvey,
				}
			];

			if(this.store.Fhir.State.Context.LaunchedByResource === 'Practitioner'){
				items.unshift({
					label: 'Add Visit',
					icon: 'pi pi-plus',
					command: this.displayVisitForm,
				},
				{
					label: 'Discharge',
					icon: 'pi pi-times',
					command: this.displayDischargeForm,
				})
			}

			return items;
		}
    },
    methods: {
		toggle(event: any) {
            this.$refs[`episode_option_menu_${this.selectedEpisode.EpisodeId}`].toggle(event);
			event.stopPropagation();
        },
		errorEvent(error: any){
			this.error = error;
		},
		async submitExternalEpisodeIdEdit(event: any) {
            try {
                if (this.selectedEpisode.ExternalEpisodeId !== this.inContextExternalIdOption?.id && this.inContextExternalIdOption){
					this.store.AddLog(`Updating external id for episode ${this.selectedEpisode.EpisodeId} with the new value of ${this.inContextExternalIdOption?.id}`);

                    this.$Progress.start();
                    this.updatingExternalId = true;
    
                    const result = await this.store.Episode.DeleteClaim(
                        this.selectedEpisode.EpisodeId,
                        this.selectedEpisode.ExternalEpisodeId,
                    );
    
                    if (result)
                        await this.store.Episode.Claim(this.selectedEpisode.EpisodeId, this.inContextExternalIdOption?.id);
    
                    this.$refs.extEpisodeIdOp.toggle(event);
                    this.updatingExternalId = false;
                    this.$Progress.finish();
                }
            } catch (e) {
                this.updatingExternalId = false;
				Promise.reject(e);
            }
        },
		toggleExtEpisodeIdOp(event: any) {
            this.$refs.extEpisodeIdOp.toggle(event);
        },
		generateEpisodeDetailHeader(episode: Episode) {
            let content = `Episode Details for ${this.patient.Name}`;

            if (this.patient.Alias) content += ` (${this.patient.Alias})`;
            if (episode.CareTypeText) content += ` - ${episode.CareTypeText}`;
            if (episode.BodyPartText) content += ` - ${episode.BodyPartText}`;

            return content;
        },
        episodesWithStatusCount(info: EpisodeInfo) {
            return this.store.Episode.State.Episodes.filter((e: Episode) => {
                return e.Info === info;
            }).length;
        },
        toggleCard(info: EpisodeInfo) {
			const index = this.d_activeEpisodeFilters.indexOf(info);
			index > -1 ? this.d_activeEpisodeFilters.splice(index, 1) : this.d_activeEpisodeFilters.push(info);

			this.$emit('CardToggle', info);
        },
        formatDateTime(date: string) {
            const newDate = new Date(date);

            if (date !== null && newDate.getFullYear() !== 1)
                return newDate.getMonth() + 1 + '/' + newDate.getDate() + '/' + newDate.getFullYear();
        },
        displayVisitForm() {
            this.$emit('DisplayVisit', this.selectedEpisode.ExternalEpisodeId);
        },
        displayDischargeForm() {
			this.$emit('DisplayDischarge', this.selectedEpisode.ExternalEpisodeId);
        },
        displaySurvey() {
            this.$emit('DisplaySurvey', this.selectedEpisode);
        },
        onRowExpand(event: any) {
            this.selectedEpisode = this.store.Episode.Get(event.data.EpisodeId);
			this.inContextExternalIdOption = this.episodesOfCare.filter((eoc: EpisodeOfCare) => this.selectedEpisode.ExternalEpisodeId.startsWith(eoc.id))[0];
			this.store.SetContextValue("SelectedEpisode", this.selectedEpisode);
        },
    },
});
</script>