<template>
    <template v-if="surveys.length > 0">
        <div class="p-col-12 p-t-0">
            <h3 class="artifact-detail-header">Surveys</h3>
        </div>
        <div class="p-col-12 p-b-0 survey-container">
            <TabView :scrollable="true" v-model:activeIndex="activeIndex">
                <TabPanel v-for="(survey, count) in surveys" :key="survey" :header="survey">
                        <div key="1" v-if="!selectedSurvey" class="p-grid">
                            <div class="p-col-12">
                                <DataTable class="p-datatable-sm" :autoLayout='true' :value="surveyValues" :paginator="true" :rows="5" :filters="filters">
                                    <template #header>
                                        Patient responses to {{survey}} were as follows
                                    </template>
                                    <Column :sortable="true" field="Attribute" header="Attribute"></Column>
                                    <Column :sortable="true" field="Value" header="Answer"></Column>
                                </DataTable>
                            </div>
                        </div>
                        <div key="2" class="p-grid" v-else>
                            <div class="p-col-3" :id="`score-list-container-${count}`">
                                <DataTable class="p-datatable-sm" :autoLayout='true' :value="scores" responsiveLayout="scroll">
                                    <Column field="Label" header="Patient Scores"></Column>
                                    <Column field="FunctionalScore" header=""></Column>
                                </DataTable>
                                <Chart v-if="scores?.length > 1" type="line" :data="chartData" :options="basicOptions" />
                            </div>
                            <div class="p-col-9" v-if="surveyValues.length > 0">
                                <DataTable class="p-datatable-sm" :autoLayout='true' :value="surveyValues" header="Responses" responsiveLayout="scroll" :paginator="true" :rows="5" :filters="filters">
                                    <template #header>
                                        Patient responses to {{selectedSurvey.Descriptor}} were as follows
                                    </template>
                                    <ColumnGroup type="header">
                                        <Row>
                                            <Column header="Activity (Question)" :rowspan="9" :sortable="true" field="Question" />
                                            <Column header="Response at" :colspan="3" />
                                        </Row>
                                        <Row>
                                            <Column header="Intake" />
                                            <Column header="Status" />
                                            <Column header="Predicted" />
                                        </Row>
                                    </ColumnGroup>
                                
                                    <Column field="Question" :sortable="true">
                                        <template #body="slotProps">
                                            <span class="br-spc-t">{{slotProps.data.Indicator}} {{slotProps.data.Question}}</span>
                                        </template>
                                    </Column>
                                    <Column field="IntakeResponse" />
                                    <Column field="StatusResponse">
                                        <template #body="slotProps">
                                            {{slotProps.data.StatusResponse === null ? '--' : slotProps.data.StatusResponse}}
                                        </template>
                                    </Column>
                                    <Column>
                                        <template #body="slotProps">
                                            {{getPredictedAnswer(slotProps.data.Question)}}
                                        </template>
                                    </Column>
                                </DataTable>
                            </div>
                        </div>
                </TabPanel>
            </TabView>
        </div>
    </template>
</template>

<script lang="ts">
import { ChartType } from 'chart.js';
import { defineComponent, ref, inject } from '@vue/runtime-core';
import { PropType } from 'vue';
import { FilterMatchMode } from 'primevue/api';
import Artifact from '../../models/foto/artifact';
import { FunctionalAssessment } from '../../models/foto/functionalAssessment';
import { PatientReported } from '../../models/foto/patientReported';
import { StagingFunctionalityPrediction } from '../../models/foto/stagingFunctionalityPrediction';

export default defineComponent({
    name: 'RiskCriteria',
    props: {
        artifact: Object as PropType<Artifact>,
    },
    setup() {
        const basicOptions = ref({
            layout: {
                padding: 5,
            },
            elements: {
                point: {
                    radius: 1,
                },
            },
            plugins: {
                legend: {
                    display: false,
                },
                tooltip: {
                    enabled: false,
                    padding: 0,
                },
            },
            scales: {
                x: {
                    ticks: {
                        display: false,
                    },
                    grid: {
                        display: false,
                        borderWidth: 0,
                    },
                },
                y: {
                    ticks: {
                        display: false,
                    },
                    grid: {
                        display: false,
                        borderWidth: 0,
                    },
                },
            },
        });

        return { 
            basicOptions, 
            store: inject('store') 
        };
    },
    data() {
        return {
            surveys: [],
            activeIndex: 0,
            filters: {
                global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            },
        };
    },
    created() {
        let surveys = this.artifact.FunctionalAssessments.map((i: FunctionalAssessment) => i.Descriptor);

        surveys.push(
            ...this.artifact.PatientReportedItems.filter((i: PatientReported) => i.Survey.toLowerCase() !== 'na').map(
                (i: PatientReported) => i.Survey,
            ),
        );

        this.surveys = [...new Set(surveys)];
    },
    computed: {
        selectedSurvey() : FunctionalAssessment {
            this.store.AddLog(`Viewing survey ${this.surveys[this.activeIndex]}`);

            return this.artifact.FunctionalAssessments.filter(
                (i: FunctionalAssessment) =>
                    i.Descriptor.toLowerCase() === this.surveys[this.activeIndex].toLowerCase(),
            )[0];
        },
        surveyValues() {
            return this.selectedSurvey
                ? this.selectedSurvey.PatientResponses
                : this.artifact.PatientReportedItems.filter(
                      (i: PatientReported) => i.Survey.toLowerCase() === this.surveys[this.activeIndex].toLowerCase(),
                  );
        },
        scores() {
            return this.selectedSurvey?.FunctionalScoreSummaries;
        },
        chartData() {
            const scores = this.scores?.map((s: any) => s.FunctionalScore);
            let borderColor = '';
            let backgroundColor = '';

            if (scores.length > 0 && scores[0] > scores[scores.length - 1]) {
                borderColor = '#e91e63';
                backgroundColor = '#fef4f7';
            } else {
                borderColor = '#689f38';
                backgroundColor = '#f7faf5';
            }

            return {
                labels: this.scores?.map(() => ''),
                datasets: [
                    {
                        label: '',
                        type: 'shadowLine' as ChartType,
                        data: scores,
                        fill: false,
                        borderWidth: 4,
                        borderColor: borderColor,
                        backgroundColor: backgroundColor,
                    },
                ],
            };
        },
    },
    methods: {
        getPredictedAnswer(question: String) {
            const prediction = this.selectedSurvey.StagingInfo?.FunctionalityPredictions.filter(
                (fp: StagingFunctionalityPrediction) => fp.Question.toLowerCase() === question.toLowerCase(),
            );

            if (prediction && prediction.length > 0) return prediction[0].Answer;

            return '--';
        },
    },
});
</script>